import React, { useState } from 'react';
import ImageList from '../../utils/ImageList';
import './TopHeader.scss';

function TopHeader() {
  const [searchValue, setSearchValue] = useState('');

  const handleInputChange = (e) => {
    setSearchValue(e.target.value);
  };

  return (
    <div className="topHeader">
      <div className="headerRight">
        <img src={ImageList.BCILogo} alt="bci-logo" className="logo" />
        <div className="loginForm">
          <div>
            <div className="inputSearch">
              <img src={ImageList.SearchIcon} alt="search-icon" className="searchIcon" />
              <input
                type="text"
                id="search"
                placeholder="Search By Company name/ Phone No."
                onChange={handleInputChange}
                value={searchValue}
              />
              <div className="searchBtnContainer">

              <button className="searchBtn">Search</button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="avatar">
        <img src={ImageList.AvatarIcon} alt="" className="avatarIcon" />
        <p className="number">1234567890</p>
      </div>
    </div>
  );
}

export default TopHeader;
