import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";

import Login from "./pages/Login/Login";
import NewDepartment from "./pages/NewDepartment/NewDepartment";
import AddRoles from "./pages/AddRoles/AddRoles";
import AddEmployee from "./pages/AddEmployee/AddEmployee";
import AddHoliday from "./pages/AddHoliday/AddHoliday";
import PermissionManagement from "./pages/PermissionManagement/PermissionManagement";

import DelegationCreateTask from "./pages/DelegationCreateTask/DelegationCreateTask";
import DelegationAlllTask from "./pages/DelegationAlllTask/DelegationAlllTask";

import DoerChecklist from "./pages/DoerChecklist/DoerChecklist";
import ProcessChecklist from "./pages/ProcessChecklist/ProcessChecklist";

import CreateProcessChecklist from "./pages/CreateProcessChecklist/CreateProcessChecklist";
import CreateDoerChecklist from "./pages/CreateDoerChecklist/CreateDoerChecklist";

import AdminDashboard from "./pages/AdminDashboard/AdminDashboard";

import Departments from "./pages/Departments/Departments";
import Roles from "./pages/Roles/Roles";
import Employees from "./pages/Employees/Employees";
import Holiday from "./pages/Holiday/Holiday";
import Permissions from "./pages/Permissions/Permissions";

import AdminAllTasks from "./pages/AdminAllTasks/AdminAllTasks";

import CreateEditDepartment from "./pages/CreateEditDepartment/CreateEditDepartment";
import CreateEditRoles from "./pages/CreateEditRoles/CreateEditRoles";
import CreateEditEmployees from "./pages/CreateEditEmployees/CreateEditEmployees";
import CreateEditHolidays from "./pages/CreateEditHolidays/CreateEditHolidays";

function router() {
  return (
    <div>
      <BrowserRouter>
        <Routes>
          <Route path="/:id" element={<Login />} />
          <Route path="/dashboard" element={<AdminDashboard />} />
          <Route path="/new-department" element={<NewDepartment />} />
          <Route path="/add-roles" element={<AddRoles />} />
          <Route path="/add-employee" element={<AddEmployee />} />
          <Route path="/add-holiday" element={<AddHoliday />} />
          <Route path="/permission-management" element={<PermissionManagement />} />

          <Route path="/departments" element={<Departments />} />
          <Route path="/roles" element={<Roles />} />
          <Route path="/employees" element={<Employees />} />
          <Route path="/holiday-list" element={<Holiday />} />
          <Route path="/permissions" element={<Permissions />} />

          <Route path="/admin-all-tasks" element={<AdminAllTasks />} />

          <Route path="/departments/:type/:id" element={<CreateEditDepartment />} />
          <Route path="/departments/:type" element={<CreateEditDepartment />} />

          <Route path="/roles/:type/:id" element={<CreateEditRoles />} />
          <Route path="/roles/:type" element={<CreateEditRoles />} />

          <Route path="/employees/:type/:id" element={<CreateEditEmployees />} />
          <Route path="/employees/:type" element={<CreateEditEmployees />} />

          <Route path="/holiday-list/:type/:id" element={<CreateEditHolidays />} />
          <Route path="/holiday-list/:type" element={<CreateEditHolidays />} />


          <Route path="/create-task" element={<DelegationCreateTask />} />
          <Route path="/all-tasks" element={<DelegationAlllTask />} />
          <Route path="/doer-checklist" element={<DoerChecklist />} />
          <Route path="/process-checklist" element={<ProcessChecklist />} />
          <Route path="/doer-checklist/create-task" element={<CreateDoerChecklist />} />
          <Route path="/process-checklist/create-checklist" element={<CreateProcessChecklist />} />
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default router;
