import React, { useState, useRef } from "react";
import TopHeader from "../../components/TopHeader/TopHeader";
import ImageList from "../../utils/ImageList";
import "./AddHoliday.scss";
import { useNavigate } from "react-router-dom";
import Cookies from "js-cookie";
import { showToasterMessage } from "../../utils/helper";
import newHoliday from "../../interface/implementation/newHolidayImp";
import { uploadHolidayCSV } from "../../interface/implementation/uploadHolidayCSVAPIImp";
import weekConfiguration from "../../interface/implementation/weekConfigurationAPIImp";
import createNonWorkingDay from "../../interface/implementation/nonWorkingDayAPIImp";
function AddHoliday() {
  const navigate = useNavigate();

  const showToast = (errorText) => {
    showToasterMessage(errorText);
  };

  const [holidayInfo, setHolidayInfo] = useState([
    {
      holiday_name: "",
      holiday_start_date: "",
      holiday_end_date: "",
    },
  ]);

  const [firstWeek, setFirstWeek] = useState();
  const [workingDay, setWorkingDay] = useState();
  const [nonWorkingdays, setNonWorkingDays] = useState([]);
  const [isChecked, setIsChecked] = useState(true);
  const [intialNonWorkingDay, setInitialNonWorkingDay] = useState("")

  const handleInputChange = (event, index) => {
    let data = [...holidayInfo];
    data[index][event.target.id] = event.target.value;
    setHolidayInfo(data);
  };

  const handleCheckboxChange = () => {
    setIsChecked(!isChecked);
  };

  const handleNextButtonClick = async () => {
    if (activeItemId === 2) {
      const fileInput = fileInputRef.current;
      if (fileInput.files.length > 0) {
        const formData = new FormData();
        console.log("file data", fileInput.files[0]);
        formData.append("holiday", fileInput.files[0]);
        // formData.append("batch_id", params.batchId)
        console.log("form data", formData);
        uploadHolidayCSV(formData, token)
          .then((res) => {
            console.log("File uploaded successfully!");
            navigate("/permission-management");
          })
          .catch((error) => {
            console.log("error during newHoliday post:", error);
            showToast("An error occurred during new Holiday creation");
          });
      }
    } else {
      if (holidayInfo.length <= 0) {
        showToasterMessage("Please add atleast one holiday");
      } else if(nonWorkingdays.length <= 0){
        showToasterMessage("Please add atleast one non working day");
      } else if (workingDay === "" || firstWeek === ""){
        showToasterMessage("Either first working day or starting week entry is missing");
      }           
      else {
      let body = [...holidayInfo];
      let newBody = {
        week_starting_day: workingDay,
        starting_date: firstWeek,
      };

      let nonWorkingDay = nonWorkingdays
      weekConfiguration(newBody, token)
        .then((res) => {
          // navigate("/permission-management");
        })
        .catch((error) => {
          console.log("error during newHoliday post:", error);
          showToast("An error occurred during new Holiday creation");
        });
      createNonWorkingDay(nonWorkingDay, token).then((res) => {
        // navigate("/permission-management");
      })
      .catch((error) => {
        console.log("error during newHoliday post:", error);
        showToast("An error occurred during new Holiday creation");
      });
      newHoliday(body, token)
        .then((res) => {
          navigate("/permission-management");
        })
        .catch((error) => {
          console.log("error during newHoliday post:", error);
          showToast("An error occurred during new Holiday creation");
        });
      }
    }
  };

  const addFields = () => {
    let object = {
      holiday_name: "",
      holiday_start_date: "",
      holiday_end_date: "",
    };
    setHolidayInfo([...holidayInfo, object]);
  };

  const removeFields = (index) => {
    let data = [...holidayInfo];
    data.splice(index, 1);
    setHolidayInfo(data);
  };

  // <---------------------------------- file upload ---------------------------------->

  const [formData, setFormData] = useState({
    documentFiles: [],
  });
  const fileInputRef = useRef();
  const [documentFiles, setDocumentFiles] = useState([]);

  const [activeItemId, setActiveItemId] = useState(1);
  const token = Cookies.get("token");

  const [file, setFile] = useState();

  const handleFileChange = (e) => {
    const files = Array.from(e.target.files).map((file) => file.name);
    console.log("upoaded files", files);
    setDocumentFiles((prevFiles) => [...prevFiles, ...files]);
  };

  // const handleRemoveFile = (index) => {
  //     setDocumentFiles((prevFiles) => {
  //         const updatedFiles = [...prevFiles];
  //         updatedFiles.splice(index, 1);
  //         return updatedFiles;
  //     });
  // };

  const handleUploadButtonClick = () => {
    fileInputRef.current.click();
  };

  const handleBodyNavigationClick = (itemId) => {
    setActiveItemId(itemId);
  };

  const handleRemoveFile = (index) => {
    let data = [...holidayInfo];
    data.splice(index, 1);
    setHolidayInfo(data);
  };

  // Function to handle form submission

  const bodyNavigationItems = [
    { id: 1, name: "Single Add" },
    { id: 2, name: "Bulk Add" },
  ];

  const handleNonWorkingDayChange = (e) => {
    console.log("value of that day", e.target.value)
    setInitialNonWorkingDay(e.target.value)
    const selectedNonWorkingDay = e.target.value;
    setNonWorkingDays(prevNonWorkingDays => [
      ...prevNonWorkingDays,
      { non_working_day: selectedNonWorkingDay }
    ]);
  };

  const handleRemoveNonWorkingDay = (index) => {
    const updatedNonWorkingDays = [...nonWorkingdays];
    updatedNonWorkingDays.splice(index, 1);
    setNonWorkingDays(updatedNonWorkingDays);
  };

  return (
    <div className="addHoliday">
      <TopHeader />
      <div className="addHolidayBody">
        {/* <h2>Add New Holiday</h2> */}
        <div className="topNavigation">
          <p className="item ">Department</p>
          <p className="item ">Roles</p>
          <p className="item ">Employees</p>
          <p className="item active">Holiday List</p>
          <p className="item">Permissions</p>
        </div>

        <div className="addHolidayContainer">
          <h2>Add Holidays</h2>

          <div className="headerInputs">
            <div className="filterContainer">
              <div className="formInput">
                <label htmlFor="name">Non Working Days</label>
                <div className="inputField">
                  <select
                    id="nonworkingdays"
                    onChange={handleNonWorkingDayChange}
                    value={intialNonWorkingDay}
                  >
                    <option value="All Sundays">All Sundays</option>
                    <option value="All Saturdays">All Saturdays</option>
                    <option value="1st Saturdays">1st Saturdays</option>
                    <option value="2nd Saturdays">2nd Saturdays</option>
                    <option value="3rd Saturdays">3rd Saturdays</option>
                    <option value="4th Saturdays">4th Saturdays</option>
                  </select>
                </div>
              </div>
              <div className="filters">
                {nonWorkingdays?.map((day, index)=>{
                  return (
                  <p key={index} className="filterItem">
                  {day.non_working_day}
                  <img 
                    src={ImageList.CrossBlue} 
                    onClick={() => handleRemoveNonWorkingDay(index)}
                    alt=""
                  />
                </p>
                  )
                })
              }
              
              
             
              </div>
            </div>
            <div className="filterContainer">
              <div className="formInput">
                <label htmlFor="name">First Working Day</label>
                <div className="inputField">
                  <select
                    id="workingDays"
                    onChange={(e) => setWorkingDay(e.target.value)}
                    value={formData.day}
                  >
                    <option value="Sunday">Sunday</option>
                    <option value="Monday">Monday</option>
                    <option value="Tuesday">Tuesday</option>
                    <option value="Wednesday">Wednesday</option>
                    <option value="Thursday">Thursday</option>
                    <option value="Friday">Friday</option>
                    <option value="Saturday">Saturday</option>
                  </select>
                </div>
              </div>
            </div>
            <div className="filterContainer">
              <div className="formInput">
                <label htmlFor="name">Starting Week</label>
                <div className="inputField">
                  <input
                    type="date"
                    id="name"
                    onChange={(e) => setFirstWeek(e.target.value)}
                    value={firstWeek}
                    placeholder="Search and select"
                  />
                </div>
              </div>
            </div>
          </div>

          {/* <div className="bodyNavigation">
            {bodyNavigationItems.map((item) => (
              <p
                key={item.id}
                className={`title ${activeItemId === item.id ? "active" : ""}`}
                onClick={() => handleBodyNavigationClick(item.id)}
              >
                {item.name}
              </p>
            ))}
          </div> */}

          {/* ******************************************** mapping ********************************** */}

          {holidayInfo.map((holiday, index) => {
            return (
              <div className="innerContainer" key={index}>
                <div className="formFields">
                  <div className="formInput">
                    <label htmlFor="name">Holiday Name *</label>
                    <div className="inputField">
                      <input
                        id="holiday_name"
                        placeholder="Enter holiday name"
                        value={holidayInfo[index].holiday_name}
                        onChange={(event) => handleInputChange(event, index)}
                      />
                    </div>
                  </div>
                  <div className="formInput">
                    <label htmlFor="startDate">Start Date</label>
                    <div className="inputField">
                      <input
                        type="date"
                        id="holiday_start_date"
                        value={holidayInfo[index].holiday_start_date}
                        onChange={(event) => handleInputChange(event, index)}
                      />
                    </div>
                  </div>
                  <div className="formInput">
                    <label htmlFor="endDate">End Date</label>
                    <div className="inputField">
                      <input
                        type="date"
                        id="holiday_end_date"
                        disabled={isChecked}
                        value={holidayInfo[index].holiday_end_date}
                        onChange={(event) => handleInputChange(event, index)}
                      />
                    </div>
                  </div>
                  <div className="formInput">
                    <label htmlFor="sameAsStartDate" className="checkboxInput">
                      <input
                        type="checkbox"
                        checked={isChecked}
                        onChange={handleCheckboxChange}
                      />
                      Same as start date
                    </label>
                  </div>
                </div>
                <div className="formActions">
                  <p className="add" onClick={addFields}>
                    <img src={ImageList.AddBlueIcon} alt="" /> Add new
                  </p>
                  <p className="delete" onClick={() => removeFields(index)}>
                    <img src={ImageList.DeleteRedIcon} alt="" /> Delete
                  </p>
                </div>
              </div>
            );
          })}

          {activeItemId === 2 && (
            <div className="bulkUpload">
              <div className="first row">
                <p className="bullet">1:</p>
                <p className="firstTitle">Download Template Employee List</p>
                <div className="downloadBtn">
                  {/* // eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                  <a href="#">
                    <img src={ImageList.DownloadIcon} alt="" />
                    Download
                  </a>
                </div>
              </div>

              <div className="second row">
                <p className="bullet">2:</p>
                <div className="content">
                  <h6>Add or Edit the data in the template</h6>
                </div>
              </div>

              <div className="third row">
                <p className="bullet">3:</p>
                <div className="employeeDetails" style={{ overflowX: "auto" }}>
                  <table className="employeeTable">
                    <thead>
                      <tr>
                        <th>Employee Name</th>
                        <th>Department</th>
                        <th>Role</th>
                        <th>Employee Type</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>Employee Name</td>
                        <td>Department</td>
                        <td>Role</td>
                        <td>Employee Type</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>

              <div className="fourth row">
                <p className="bullet">4:</p>
                <div className="uploadContent">
                  <p className="title">Upload</p>
                  <div className="uploadArea">
                    <p className="titleHomework">Upload Employee List *</p>
                    <div className="uploadContainer">
                      <input
                        type="file"
                        name=""
                        id="fileInput"
                        ref={fileInputRef}
                        onChange={handleFileChange}
                        style={{ display: "none" }}
                      />
                      <button
                        className="uploadHomeworkBtn"
                        onClick={handleUploadButtonClick}
                      >
                        <img src={ImageList.WhiteUploadIcon} alt="" />
                        Upload
                      </button>
                      <div className="fileNames">
                        {documentFiles.map((fileName, index) => (
                          <p key={index} className="fileName">
                            {fileName}
                            <span
                              className="removeBtn"
                              onClick={() => handleRemoveFile(index)}
                            >
                              x Remove
                            </span>
                          </p>
                        ))}
                      </div>
                      {documentFiles.length === 0 && (
                        <div>
                          <p className="uploadInfo">No file chosen</p>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}

          <div>
            {/* <button onClick={handleFileUpload} className="nextButton">Next<img src={ImageList.ArrowForwardWhite} alt="" /></button> */}
            <button onClick={handleNextButtonClick} className="nextBtn">
              Next
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default AddHoliday;
