import checkRoleAPI from "../api/checkRoleAPI";

export default function checkRole(token) {
  // console.log(accessToken);
  return checkRoleAPI
      .get(token)
      .then((res) => {
        console.log("Response ", res);
        return res; 
    })
    .catch((err) => {
        console.log("Error", err);
        throw err; 
    });
  
}