import React, { useState, useRef, useEffect } from "react";
import TopHeader from "../../components/TopHeader/TopHeader";
import ImageList from "../../utils/ImageList";
import "./AddEmployee.scss";
import { useNavigate } from "react-router-dom";
import Cookies from "js-cookie";
import { showToasterMessage } from "../../utils/helper";
import newEmp from "../../interface/implementation/newEmpImp";
import { uploadEmployeeCSV } from "../../interface/implementation/uploadEmployeeCSVAPIImp";
import getDeptList from "../../interface/implementation/getDeptListImp";
import getroleList from "../../interface/implementation/getRoleListImp";

function AddEmployee() {
  const navigate = useNavigate();

  const showToast = (errorText) => {
    showToasterMessage(errorText);
  };
  const token = Cookies.get("token");

  const [employeeData, setEmployeeData] = useState([
    {
      emp_name: "",
      dept_id: "",
      role_id: "",
      emp_email: "",
      phone_no: "",
      joining_date: "",
      dept_name: "", // Add dept_name field
      role_name: "", // Add role_name field
    },
  ]);

  const [dept, setDept] = useState([
    {
      dept_id: "",
      dept_name: "",
    },
  ]);

  const [role, setRole] = useState([
    {
      role_id: "",
      role_name: "",
    },
  ]);

  useEffect(() => {
    // call depatment api
    const getAllDeptList = () => {
      const token = Cookies.get("token");
      getDeptList(token)
        .then((res) => {
          const mappedDepartments = res.result.map((department) => ({
            dept_id: department.dept_id || "",
            dept_name: department.dept_name || "",
          }));
          setDept(mappedDepartments);
        })
        .catch((error) => {
          console.log("error during getting deptList:", error);
          showToast("An error occurred during fetching All department List");
        });
    };
    getAllDeptList();

    //call roles api
    const getAllRoleList = () => {
      const token = Cookies.get("token");
      getroleList(token)
        .then((res) => {
          const mappedRoles = res.result.map((role) => ({
            role_name: role.role_name || "", // Use empty string if null
            role_id: role.role_id || "", // Use empty string if null
          }));
          setRole(mappedRoles);
        })
        .catch((error) => {
          console.log("error during getting roleList:", error);
          showToast("An error occurred during fetching All role List");
        });
    };
    getAllRoleList();
  }, []);

  // const handleInputChange = (e, index) => {
  //   const { id, value } = e.target;
  //   setEmployeeData((prevEmployeeData) => {
  //     const updatedEmployeeData = [...prevEmployeeData];
  //     updatedEmployeeData[index] = {
  //       ...updatedEmployeeData[index],
  //       [id]: value,
  //     };
  //     return updatedEmployeeData;
  //   });
  // };

  const handleInputChange = (e, index) => {
    const { id, value } = e.target;
    console.log(id, value);
    setEmployeeData((prevEmployeeData) => {
      const updatedEmployeeData = [...prevEmployeeData];
      updatedEmployeeData[index] = {
        ...updatedEmployeeData[index],
        [id]: value,
      };
      // Update dept_name when dept_id changes
      if (id === "dept_id") {
        console.log(dept);
        // const selectedDept = dept.find((d) => d.dept_id === value);
        const selectedDept = dept;
        console.log(selectedDept);
        updatedEmployeeData[index] = {
          ...updatedEmployeeData[index],
          dept_name: selectedDept ? selectedDept.dept_name : "",
        };
      }
      // Update role_name when role_id changes
      if (id === "role_id") {
        const selectedRole = role.find((r) => r.role_id === value);
        updatedEmployeeData[index] = {
          ...updatedEmployeeData[index],
          role_name: selectedRole ? selectedRole.role_name : "",
        };
      }
      return updatedEmployeeData;
    });
  };

  // submit
  const handleNextButtonClick = () => {
    if (employeeData[0].emp_name.length <= 0) {
      showToast("Please enter atleast one employee");
    } else {
      let body = [...employeeData];

      const token = Cookies.get("token");

      newEmp(body, token)
        .then((res) => {
          showToast(res);
          navigate("/add-holiday");
        })
        .catch((error) => {
          console.log("error during newDept post:", error);
          showToast("An error occurred during new Department creation");
        });
    }
    // if (activeItemId === 2) {
    //   const fileInput = fileInputRef.current;
    //   if (fileInput.files.length > 0) {
    //     const formData = new FormData();
    //     console.log("file data", fileInput.files[0]);
    //     formData.append("employee", fileInput.files[0]);
    //     // formData.append("batch_id", params.batchId)
    //     console.log("form data", formData);
    //     uploadEmployeeCSV(formData, token)
    //       .then((res) => {
    //         console.log("File uploaded successfully!");
    //         navigate("/permission-management");
    //       })
    //       .catch((error) => {
    //         console.log("error during newHoliday post:", error);
    //         showToast("An error occurred during new Holiday creation");
    //       });
    //   }
    // } else {
    //   if (employeeData.length <= 0) {
    //     showToast("Please enter atleast one Employee");
    //   } else {
    //     let body = [...employeeData];

    //     const token = Cookies.get("token");

    //     newEmp(body, token)
    //       .then((res) => {
    //         navigate("/add-holiday");
    //       })
    //       .catch((error) => {
    //         console.log("error during newDept post:", error);
    //         showToast("An error occurred during new Department creation");
    //       });
    //   }
    // }
  };

  const addFields = () => {
    let object = {
      dept_name: "",
      dept_head: "",
    };
    setEmployeeData([...employeeData, object]);
  };

  const removeFields = (index) => {
    let data = [...employeeData];
    data.splice(index, 1);
    setEmployeeData(data);
  };

  //  <------------------------------------------ file upload ------------------------------------------>
  const [activeItemId, setActiveItemId] = useState(1);
  const fileInputRef = useRef();
  const [documentFiles, setDocumentFiles] = useState([]);

  const handleFileChange = (e) => {
    const files = Array.from(e.target.files).map((file) => file.name);
    setDocumentFiles((prevFiles) => [...prevFiles, ...files]);
    console.log(documentFiles);
  };

  const handleRemoveFile = (index) => {
    setDocumentFiles((prevFiles) => {
      const updatedFiles = [...prevFiles];
      updatedFiles.splice(index, 1);
      return updatedFiles;
    });
  };

  const handleUploadButtonClick = () => {
    fileInputRef.current.click();
  };

  const handleBodyNavigationClick = (itemId) => {
    setActiveItemId(itemId);
  };

  const bodyNavigationItems = [
    { id: 1, name: "Single Add" },
    { id: 2, name: "Bulk Add" },
  ];

  return (
    <div className="addEmployee">
      <TopHeader />
      <div className="addEmployeeBody">
        {/* <h2>Add New Employee</h2> */}
        <div className="topNavigation">
          <p className="item">Department</p>
          <p className="item">Roles</p>
          <p className="item active">Employees</p>
          <p className="item">Holiday List</p>
          <p className="item">Permissions</p>
        </div>

        <div className="addEmployeeContainer">
          <h2>Add New Employee</h2>

          {/* <div className="bodyNavigation">
            {bodyNavigationItems.map((item) => (
              <p
                key={item.id}
                className={`title ${activeItemId === item.id ? "active" : ""}`}
                onClick={() => handleBodyNavigationClick(item.id)}
              >
                {item.name}
              </p>
            ))}
          </div> */}

          {activeItemId === 1 && (
            <>
              {employeeData.map((form, index) => {
                return (
                  <div className="addSingle">
                    <div className="formFields">
                      <div className="formInput">
                        <label htmlFor="name">Employee Name</label>
                        <div className="inputField">
                          <input
                            id="emp_name"
                            value={employeeData[index].emp_name}
                            onChange={(e) => handleInputChange(e, index)}
                            placeholder="Enter employee name"
                          />
                        </div>
                      </div>
                      <div className="formInput">
                        <label htmlFor="department">Department</label>
                        <div className="inputField">
                          {/* <input
                            id="dept_name"
                            onChange={(e) => handleInputChange(e, index)}
                            value={employeeData[index].dept_name}
                            placeholder="Enter department"
                          /> */}
                          <select
                            id="dept_id"
                            value={form.dept_id}
                            onChange={(e) => handleInputChange(e, index)}
                          >
                            <option value="">Select department</option>
                            {dept.map((dept) => (
                              <option key={dept.dept_id} value={dept.dept_id}>
                                {dept.dept_name}
                              </option>
                            ))}
                          </select>
                        </div>
                      </div>
                      <div className="formInput">
                        <label htmlFor="role">Role</label>
                        <div className="inputField">
                          {/* <input
                            id="role_name"
                            onChange={(e) => handleInputChange(e, index)}
                            value={employeeData[index].role_name}
                            placeholder="Enter role"
                          /> */}
                          <select
                            id="role_id"
                            value={form.role_id}
                            onChange={(e) => handleInputChange(e, index)}
                          >
                            <option value="">Select role</option>
                            {role.map((role) => (
                              <option key={role.role_id} value={role.role_id}>
                                {role.role_name}
                              </option>
                            ))}
                          </select>
                        </div>
                      </div>
                      <div className="formInput">
                        <label htmlFor="email">Email</label>
                        <div className="inputField">
                          <input
                            type="email"
                            id="emp_email"
                            onChange={(e) => handleInputChange(e, index)}
                            value={employeeData[index].emp_email}
                            placeholder="Enter email"
                          />
                        </div>
                      </div>
                      <div className="formInput">
                        <label htmlFor="phone">Phone</label>
                        <div className="inputField">
                          <input
                            type="tel"
                            id="phone_no"
                            onChange={(e) => handleInputChange(e, index)}
                            value={employeeData[index].phone_no}
                            placeholder="Enter phone"
                          />
                        </div>
                      </div>
                      <div className="formInput">
                        <label htmlFor="joiningDate">Joining Date</label>
                        <div className="inputField">
                          <input
                            type="date"
                            id="joining_date"
                            onChange={(e) => handleInputChange(e, index)}
                            value={employeeData[index].joining_date}
                            placeholder="Select joining date"
                          />
                        </div>
                      </div>
                    </div>
                    <div className="formActions">
                      <p className="add" onClick={addFields}>
                        <img src={ImageList.AddBlueIcon} alt="" /> Add new
                        employee
                      </p>
                      <p className="delete" onClick={() => removeFields(index)}>
                        <img src={ImageList.DeleteRedIcon} alt="" /> Delete
                      </p>
                    </div>
                  </div>
                );
              })}
            </>
          )}

          {activeItemId === 2 && (
            <div className="bulkUpload">
              <div className="first row">
                <p className="bullet">1:</p>
                <p className="firstTitle">Download Template Employee List</p>
                <div className="downloadBtn">
                  {/* // eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                  <a href="#">
                    <img src={ImageList.DownloadIcon} alt="" />
                    Download
                  </a>
                </div>
              </div>

              <div className="second row">
                <p className="bullet">2:</p>
                <div className="content">
                  <h6>Add or Edit the data in the template</h6>
                </div>
              </div>

              <div className="third row">
                <p className="bullet">3:</p>
                <div className="employeeDetails" style={{ overflowX: "auto" }}>
                  <table className="employeeTable">
                    <thead>
                      <tr>
                        <th>Employee Name</th>
                        <th>Department</th>
                        <th>Role</th>
                        <th>Employee Type</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>Employee Name</td>
                        <td>Department</td>
                        <td>Role</td>
                        <td>Employee Type</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>

              <div className="fourth row">
                <p className="bullet">4:</p>
                <div className="uploadContent">
                  <p className="title">Upload</p>
                  <div className="uploadArea">
                    <p className="titleHomework">Upload Employee List *</p>
                    <div className="uploadContainer">
                      <input
                        type="file"
                        name=""
                        id="fileInput"
                        ref={fileInputRef}
                        onChange={handleFileChange}
                        style={{ display: "none" }}
                      />
                      <button
                        className="uploadHomeworkBtn"
                        onClick={handleUploadButtonClick}
                      >
                        <img src={ImageList.WhiteUploadIcon} alt="" />
                        Upload
                      </button>
                      <div className="fileNames">
                        {documentFiles.map((fileName, index) => (
                          <p key={index} className="fileName">
                            {fileName}
                            <span
                              className="removeBtn"
                              onClick={() => handleRemoveFile(index)}
                            >
                              x Remove
                            </span>
                          </p>
                        ))}
                      </div>
                      {documentFiles.length === 0 && (
                        <div>
                          <p className="uploadInfo">No file chosen</p>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}

          <button onClick={handleNextButtonClick} className="nextBtn">
            Next
          </button>
        </div>
      </div>
    </div>
  );
}

export default AddEmployee;
