import newDeptAPI from "../api/newDeptAPI";

export default function newDept(body, token) {
  // console.log(accessToken);
  return newDeptAPI
    .post(body, token)
    .then((res) => {
      return res;
    })
    .catch((err) => {
      console.log("Error", err);
      throw err;
    });
}
