import newHolidayAPI from "../api/newHolidayAPI";

export default function newHoliday(body, token) {
  return newHolidayAPI
      .post(body, token)
      .then((res) => {
        console.log("Response ", res);
        return res; 
    })
    .catch((err) => {
        console.log("Error", err);
        throw err; 
    });
  
}
