import React, { useEffect, useState, useRef } from "react";
import TopHeader from "../../components/TopHeader/TopHeader";
import SidebarNav from "../../components/SideBar/SideBar";
import SidebarMenuItems from "../../utils/SidebarMenuItems";
import "./CreateEditHolidays.scss";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
import ImageList from "../../utils/ImageList";
import { showToasterMessage } from "../../utils/helper";
import Cookies from "js-cookie";
import newHoliday from "../../interface/implementation/newHolidayImp";
import editHolidayImp from "../../interface/implementation/editHolidayImp";
import { useLocation } from "react-router-dom";

function CreateEditHolidays() {
  const { type } = useParams();

  const [pageType, setPageType] = useState("");

  useEffect(() => {
    setPageType(type);
  }, [type]);

  const showToast = (errorText) => {
    showToasterMessage(errorText);
  };

  const location = useLocation();
  console.log(location);

  const [editHoliday, setEditHoliday] = useState(() => {
    if (type === "edit") {
      return {
        holiday_id: location.state.editHoliday.holiday_id,
        holiday_name: location.state.editHoliday.holiday_name,
        holiday_start_date: location.state.editHoliday.holiday_start_date,
        holiday_end_date: location.state.editHoliday.holiday_end_date,
      };
    } else {
      return {
        holiday_id: "",
        holiday_name: "",
        holiday_start_date: "",
        holiday_end_date: "",
      };
    }
  });

  const [holidayInfo, setHolidayInfo] = useState([
    {
      holiday_name: "",
      holiday_start_date: "",
      holiday_end_date: "",
    },
  ]);
  const [isChecked, setIsChecked] = useState(false);

  // const handleInputChange = (e, index) => {
  //   let data = [...holidayInfo];
  //   data[index][e.target.id] = e.target.value;
  //   setHolidayInfo(data);
  // };

  const handleInputChange = (e, index) => {
    const { id, value } = e.target;
    if (pageType == "edit") {
      setEditHoliday({
        ...editHoliday,
        [id]: value,
      });
    } else {
      let data = [...holidayInfo];
      data[index][e.target.id] = e.target.value;
      setHolidayInfo(data);
    }
  };

  // submit
  // const handleNextButtonClick = () => {
  //   if (holidayInfo[0].holiday_name.length <= 0) {
  //     showToast("Please enter atleast one holiday");
  //   } else {
  //     let body = [...holidayInfo];

  //     const token = Cookies.get("token");

  //     newHoliday(body, token)
  //       .then((res) => {
  //         showToast(res);
  //       })
  //       .catch((error) => {
  //         console.log("error during newHoliday post:", error);
  //         showToast("An error occurred during new Holiday creation");
  //       });
  //   }
  // };
  const handleNextButtonClick = () => {
    if (pageType === "edit") {
      let body = {
        ...editHoliday,
      };
      const token = Cookies.get("token");
      editHolidayImp(body, token)
        .then((res) => {
          showToast(res);
          //   navigate("/add-roles");
        })
        .catch((error) => {
          console.log("error editing holiday:", error);
          showToast("An error occurred during Holiday edit");
        });
    } else {
      if (holidayInfo[0].holiday_name.length <= 0) {
        showToast("Please enter atleast one holiday");
      } else {
        let body = [...holidayInfo];

        const token = Cookies.get("token");

        newHoliday(body, token)
          .then((res) => {
            showToast(res);
            //   navigate("/add-roles");
          })
          .catch((error) => {
            console.log("error during newHoliday post:", error);
            showToast("An error occurred during new Holiday creation");
          });
      }
    }
  };

  const [formData, setFormData] = useState({
    documentFiles: [],
  });

  const fileInputRef = useRef();
  const [documentFiles, setDocumentFiles] = useState([]);

  const [activeItemId, setActiveItemId] = useState(1);

  const handleFileChange = (e) => {
    const files = Array.from(e.target.files).map((file) => file.name);
    setDocumentFiles((prevFiles) => [...prevFiles, ...files]);
    console.log(documentFiles);
  };

  const handleRemoveFile = (index) => {
    setDocumentFiles((prevFiles) => {
      const updatedFiles = [...prevFiles];
      updatedFiles.splice(index, 1);
      return updatedFiles;
    });
  };

  const handleUploadButtonClick = () => {
    fileInputRef.current.click();
  };

  const handleFileUpload = () => {};

  const handleBodyNavigationClick = (itemId) => {
    setActiveItemId(itemId);
  };

  const handlePrevButtonClick = () => {};

  // const handleCheckboxChange = (e, index) => {
  //   setIsChecked(!isChecked);
  //   console.log(e.target.isChecked, index);
  // };

  const handleCheckboxChange = (index) => {
    setHolidayInfo((prevHolidayInfo) => {
      const updatedHolidayInfo = [...prevHolidayInfo];
      updatedHolidayInfo[index].holiday_end_date = !isChecked
        ? updatedHolidayInfo[index].holiday_start_date
        : "";
      return updatedHolidayInfo;
    });
    setIsChecked(!isChecked);
  };

  const addFields = () => {
    let object = {
      holiday_name: "",
      holiday_start_date: "",
      holiday_end_date: "",
    };
    setHolidayInfo([...holidayInfo, object]);
  };

  const removeFields = (index) => {
    let data = [...holidayInfo];
    data.splice(index, 1);
    setHolidayInfo(data);
  };

  const bodyNavigationItems = [
    { id: 1, name: "Single Add" },
    { id: 2, name: "Bulk Add" },
  ];

  return (
    <div className="createEditHolidays">
      <TopHeader />
      <div className="dashboardBody">
        <SidebarNav menuItems={SidebarMenuItems} />
        <div className="dashboardContent">
          <div className="headerBody">
            <h2>{pageType === "edit" ? "Edit" : "Create"} Holiday</h2>
            <div className="actionHeader">
              {/* <div className='formInput'>
                                <div className="inputField">
                                    <input
                                        type="text"
                                        id="completionDate"
                                        onChange={handleInputChange}
                                        value={""}
                                        placeholder='Search...'
                                    />
                                </div>
                            </div> */}
              <Link to={"/holiday-list"} className="tableBtn">
                {" "}
                Back
              </Link>
            </div>
          </div>
          <div className="createDepartmentContainer">
            <div className="bodyNavigation">
              {bodyNavigationItems.map((item) => (
                <p
                  key={item.id}
                  className={`title ${
                    activeItemId === item.id ? "active" : ""
                  } ${pageType === "edit" && item.id === 2 ? "disabled" : ""}`}
                  onClick={() => handleBodyNavigationClick(item.id)}
                >
                  {item.name}
                </p>
              ))}
            </div>

            {activeItemId === 1 && (
              <>
                {holidayInfo.map((form, index) => {
                  return (
                    <div className="innerContainer">
                      <div className="formFields">
                        <div className="formInput">
                          <label htmlFor="name">Holiday Name *</label>
                          <div className="inputField">
                            <input
                              id="holiday_name"
                              placeholder="Enter holiday name"
                              // value={holidayInfo[index].holiday_name}
                              value={
                                pageType === "edit"
                                  ? editHoliday.holiday_name
                                  : holidayInfo[index].holiday_name
                              }
                              onChange={(e) => handleInputChange(e, index)}
                            />
                          </div>
                        </div>
                        <div className="formInput">
                          <label htmlFor="startDate">Start Date</label>
                          <div className="inputField">
                            <input
                              type="date"
                              id="holiday_start_date"
                              // value={holidayInfo[index].holiday_start_date}
                              value={
                                pageType === "edit"
                                  ? editHoliday.holiday_start_date
                                  : holidayInfo[index].holiday_start_date
                              }
                              onChange={(e) => handleInputChange(e, index)}
                            />
                          </div>
                        </div>
                        <div className="formInput">
                          <label htmlFor="endDate">End Date</label>
                          <div className="inputField">
                            <input
                              type="date"
                              id="holiday_end_date"
                              disabled={isChecked}
                              // value={holidayInfo[index].holiday_end_date}
                              value={
                                pageType === "edit"
                                  ? editHoliday.holiday_end_date
                                  : holidayInfo[index].holiday_end_date
                              }
                              onChange={(e) => handleInputChange(e, index)}
                            />
                          </div>
                        </div>
                        <div className="formInput">
                          <label
                            htmlFor="sameAsStartDate"
                            className="checkboxInput"
                          >
                            <input
                              type="checkbox"
                              checked={isChecked}
                              onChange={() => handleCheckboxChange(index)}
                            />
                            Same as start date
                          </label>
                        </div>
                      </div>
                      {pageType === "create" && (
                        <div className="formActions">
                          <p className="add" onClick={addFields}>
                            <img src={ImageList.AddBlueIcon} alt="" /> Add new
                            Holiday
                          </p>
                          <p
                            className="delete"
                            onClick={() => removeFields(index)}
                          >
                            <img src={ImageList.DeleteRedIcon} alt="" /> Delete
                          </p>
                        </div>
                      )}
                    </div>
                  );
                })}
              </>
            )}

            {activeItemId === 2 && pageType === "create" && (
              <div className="bulkUpload">
                <div className="first row">
                  <p className="bullet">1:</p>
                  <p className="firstTitle">
                    Download Template Employee List
                  </p>
                  <div className="downloadBtn">
                    {/* // eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                    <a href="#">
                      <img src={ImageList.DownloadIcon} alt="" />
                      Download
                    </a>
                  </div>
                </div>

                <div className="second row">
                  <p className="bullet">2:</p>
                  <div className="content">
                    <h6>Add or Edit the data in the template</h6>
                  </div>
                </div>

                <div className="third row">
                  <p className="bullet">3:</p>
                  <div
                    className="employeeDetails"
                    style={{ overflowX: "auto" }}
                  >
                    <table className="employeeTable">
                      <thead>
                        <tr>
                          <th>Employee Name</th>
                          <th>Department</th>
                          <th>Role</th>
                          <th>Employee Type</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td>Employee Name</td>
                          <td>Department</td>
                          <td>Role</td>
                          <td>Employee Type</td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>

                <div className="fourth row">
                  <p className="bullet">4:</p>
                  <div className="uploadContent">
                    <p className="title">Upload</p>
                    <div className="uploadArea">
                      <p className="titleHomework">Upload Employee List *</p>
                      <div className="uploadContainer">
                        <input
                          type="file"
                          name=""
                          id="fileInput"
                          ref={fileInputRef}
                          onChange={handleFileChange}
                          style={{ display: "none" }}
                        />
                        <button
                          className="uploadHomeworkBtn"
                          onClick={handleUploadButtonClick}
                        >
                          <img src={ImageList.WhiteUploadIcon} alt="" />
                          Upload
                        </button>
                        <div className="fileNames">
                          {documentFiles.map((fileName, index) => (
                            <p key={index} className="fileName">
                              {fileName}
                              <span
                                className="removeBtn"
                                onClick={() => handleRemoveFile(index)}
                              >
                                x Remove
                              </span>
                            </p>
                          ))}
                        </div>
                        {documentFiles.length === 0 && (
                          <div>
                            <p className="uploadInfo">No file chosen</p>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            )}

            <div>
              {/* <button onClick={handleFileUpload} className="nextButton">Next<img src={ImageList.ArrowForwardWhite} alt="" /></button> */}
              <button onClick={handleNextButtonClick} className="nextBtn">
                {pageType === "edit" ? "Update" : "Save"}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CreateEditHolidays;
