import React, { useState } from 'react';
import TopHeader from '../../components/TopHeader/TopHeader';
import SidebarNav from '../../components/SideBar/SideBar';
import SidebarMenuItems from '../../utils/SidebarMenuItems';
import './Permissions.scss';
import { Link } from 'react-router-dom';
import Delegation from '../../components/Delegation/Delegation';
import Checklist from '../../components/Checklist/Checklist';
import EmployeeLeave from '../../components/UserManagement/UserManagement';
import ImageList from '../../utils/ImageList';


function Permissions() {

    const handleInputChange = () => {
        console.log("create");
    }
    const [activeItemId, setActiveItemId] = useState(1);

    const handleBodyNavigationClick = (itemId) => {
        setActiveItemId(itemId);
    };

    const bodyNavigationItems = [
        { id: 1, name: 'Delegation' },
        { id: 2, name: 'Checklist' },
        { id: 3, name: 'User Management' },
    ];

    return (
        <div className="permissions">
            <TopHeader />
            <div className="dashboardBody">
                <SidebarNav menuItems={SidebarMenuItems} />
                <div className="dashboardContent">
                    <div className="headerBody">
                        <h2>Permissions list</h2>
                        <div className="actionHeader">
                            <div className='formInput'>
                                <div className="inputField">
                                    <input
                                        type="text"
                                        id="completionDate"
                                        onChange={handleInputChange}
                                        value={""}
                                        placeholder='Search...'
                                    />
                                </div>
                            </div>
                            {/* <Link to={'/holiday/create'} className="tableBtn">+ Add New Holiday List</Link> */}
                        </div>
                    </div>
                    <div className="permissionsContainer">
                        <div className="bodyNavigation">
                            {bodyNavigationItems.map((item) => (
                                <p
                                    key={item.id}
                                    className={`title ${activeItemId === item.id ? 'active' : ''}`}
                                    onClick={() => handleBodyNavigationClick(item.id)}
                                >
                                    {item.name}
                                </p>
                            ))}
                        </div>
                        {activeItemId === 1 && <Delegation />}
                        {activeItemId === 2 && <Checklist />}
                        {activeItemId === 3 && <EmployeeLeave />}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Permissions;
