import React, { useState } from 'react';
import './TaskModal.scss';
import ImageList from '../../utils/ImageList';

function TaskModal({ onClose, title }) {

  const [status, setStatus] = useState('Inprogress');

  const handleInputChange = (e) => {
    setStatus(e.target.value);
  };

  const handleUploadButtonClick = () => {
    document.getElementById('fileInput').click();
  };

  const handleFileChange = (e) => {
    const selectedFile = e.target.files[0];
    console.log('Selected File:', selectedFile);
  };

  const handleCloseClick = () => {
    onClose();
  };

  const handleSaveClick = () => {
    console.log('saved');
    // Add logic for saving data
  };

  return (
    <div className="task-modal-overlay TaskModal">
      <div className="task-modal-content">
        <div className="task-modal-header">
          <p className='video-title'>{title}</p>
          <p className='closeBtn' onClick={handleCloseClick}>x</p>
        </div>
        <div className="body">
          <div className="inputContainer">
            {title === 'Market Research' && (
              <div className="marketResearchModal">
                <div className="inputCode">
                  <label htmlFor="selectedTaskStatus"> Task Status</label>
                  <select
                    id="selectedTaskStatus"
                    onChange={(e) => handleInputChange(e)}
                    value={status}
                  >
                    <option value="Inprogress">In Progress</option>
                    <option value="Delayed">Delayed</option>
                    <option value="Completed">Completed</option>
                  </select>
                </div>
                <div className="uploadArea">
                  <label htmlFor="fileInput" className="uploadLabel">
                    Upload Files :
                  </label>
                  <input
                    type="file"
                    id="fileInput"
                    accept=".pdf, .doc, .docx"
                    onChange={handleFileChange}
                    style={{ display: 'none' }}
                  />
                  <button className="uploadButton" onClick={handleUploadButtonClick}>
                    <img src={ImageList.UploadIcon} alt="" />
                    Upload
                  </button>
                </div>
              </div>
            )}

            {title === 'Reassign' && (
              <div className="reassignModal">
                <div className="inputCode">
                  <label htmlFor="inputDate">Change Date</label>
                  <input type="date" name="" id="inputDate" />
                </div>
              </div>
            )}

            {title === 'Task Transfer' && (
              <div className="transferModal">
                <div className="inputCode">
                  <label htmlFor="transfer">Transfer to</label>
                  <input type="text" name="" id="transfer" />
                </div>
              </div>
            )}
     
          </div>
          {title === 'Notes' && (
              <div className="notesModal">
                <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Nulla aut in reprehenderit sed fuga inventore pariatur nesciunt cupiditate natus, alias voluptatum aliquam expedita blanditiis, temporibus, ducimus labore ex maiores maxime vitae? Delectus cupiditate reprehenderit quasi vitae saepe. Voluptatem, ducimus ipsam.</p>
              </div>
            )}
        </div>
        {title !== 'Notes' &&
          (
            <div className="saveButtonWrapper">
              <button className='approveBtn' onClick={handleSaveClick}>Save</button>
            </div>
          )
        }

      </div>
    </div>
  );
}

export default TaskModal;
