import weekConfigurationAPI from "../api/weekConfigurationAPI";

export default function weekConfiguration(body, token) {
  return weekConfigurationAPI
      .post(body, token)
      .then((res) => {
        console.log("Response ", res);
        return res; 
    })
    .catch((err) => {
        console.log("Error", err);
        throw err; 
    });
  
}
