import React, { useState } from 'react';
import ImageList from '../../utils/ImageList';
import './UserManagement.scss';
import { useNavigate } from 'react-router-dom';
import Cookies from "js-cookie";
import autoCompleteRole from "../../interface/implementation/autoCompleteRoleAPIImp";
import { DoerMenu } from "../../utils/SidebarMenuItems";
import createPermissions from "../../interface/implementation/createPermissionsAPIImp";
function UserManagement({ submissionDone }) {
  const navigate = useNavigate();
  const [searchStr, setSearchStr] = useState("");
  const [roleNames, setRoleNames] = useState([]);
  const [create, setCreate] = useState([]);
  const [autoModal, setAutoModal] = useState(false)
  const [showCreateRoles, setShowCreateRoles] = useState(false);
  const [searchStrCreate, setSearchStrCreate] = useState("");
  const [searchStrView, setSearchStrView] = useState("");
  const [searchStrUpdate, setSearchStrUpdate] = useState("");
  const [searchStrAll, setSearchStrAll] = useState("");
  const [activePermissionType, setActivePermissionType] = useState(null);

  const [permissions, setPermissions] = useState({
    create: [],
    view: [],
    update: [],
    all: []
  });

  const [permissionIds, setPermissionIds] = useState({
    create: [],
    view: [],
    update: [],
    all: []
  });
  
  // Define a function to update the permission IDs
  const setValues = (values, type) => {
    setPermissionIds(prevIds => ({
      ...prevIds,
      [type]: values
    }));
  };
 
  const token = Cookies.get("token");
  const handleNextButtonClick = () => {
    // Add logic for handling next button click
    // submit everything to backend
    //call a new function which is a call back to parent
    let body = [
      {
        system_name: "checklist",
        create: permissionIds.create,
        view: permissionIds.view,
        update: permissionIds.update,
        all: permissionIds.all

      }
    ]

    createPermissions(body, token).then((res)=>{
      console.log("res", res)
    }).catch((err)=> console.log("err", err))

    console.log("body", body)
    submissionDone(4);
  };

  const handleInputChange = (e, type) => {
    const value = e.target.value;
    setActivePermissionType(type);
    if (type === "create") {
      setSearchStrCreate(value);
    } else if (type === "view") {
      setSearchStrView(value);
    } else if (type === "update") {
      setSearchStrUpdate(value);
    } else if (type === "all") {
      setSearchStrAll(value);
    }

    let body = {
      roleName: e.target.value,
    };
    autoCompleteRole(body, token)
      .then((res) => {
        console.log("role names", res.result);
        setRoleNames(res.result);
        setAutoModal(true)
      })
      .catch((err) => console.log("err", err));
  };



const handlePermissionChange = (item, type) => {
  setPermissions(prevPermissions => ({
    ...prevPermissions,
    [type]: [...prevPermissions[type], item]
  }));
  const roleId = item.role_id;
  setValues([...permissionIds[type], roleId], type);
  
  setAutoModal(false);
};

const handleRemovePermission = (item, type) => {
  const updatedPermissions = permissions[type].filter(role => role !== item);
  setPermissions(prevPermissions => ({
    ...prevPermissions,
    [type]: updatedPermissions
  }));
  const roleIdToRemove = item.role_id;
  const updatedIds = permissionIds[type].filter(id => id !== roleIdToRemove);
  setValues(updatedIds, type);
};

const renderPermissions = (type) => {
  return permissions[type].map((role, index) => (
    <p key={index} className="filterItem">
      {role.role_name}
      <img 
        src={ImageList.CrossBlue} 
        onClick={() => handleRemovePermission(role, type)} 
        alt=""
      />
    </p>
  ));
};


  return (
    <div className="userManagement">
      <div className="questionContainer">
        <div className="questionItem">
          <p className="question">Who can manage?</p>
          <div className="filterContainer">
            <div className="formInput">
              <label htmlFor="name">Search</label>
              <div className="inputField searchField " >
                <input
                  type="text"
                  id="name"
                  onChange={(e) => handleInputChange(e, "all")}
                  value={searchStrAll}
                  placeholder="Search and select"
                />
                 { roleNames?.length > 0 && autoModal && activePermissionType === "all" && (
                      <div className="searchDropdown">
                      {roleNames?.map((item, index) => (
                        <div key={index} onClick={()=>
                          
                        {
                          handlePermissionChange(item, "all")
                          setAutoModal(false)
                        
                        }}>
                        <p >{item.role_name}</p>
                        </div>
                      ))}
                    </div>
                    )}
              </div>
            </div>
            <div className="filters">
            {renderPermissions("all")}
              
            </div>
          </div>
        </div>
       
      </div>

      <button className="nextButton" onClick={handleNextButtonClick}>
        Save
      </button>
    </div>
  );
}

export default UserManagement;
