import React, { useState, useEffect } from 'react';
import ImageList from '../../utils/ImageList';
import './ManageTeamTasks.scss';
import TaskModal from '../TaskModal/TaskModal';

function ManageTeamTasks() {
  const storedAccessList = localStorage.getItem("accessList");
  const parsedAccessList = JSON.parse(storedAccessList)

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedTask, setSelectedTask] = useState(null);
  const [modalTitle, setModalTitle] = useState("");
  const [stateAccessList, setStateAccessList] = useState(parsedAccessList || {});

  // parsedAccessList.Delegation = {
  //   create: false,
  //   view: false,
  //   update: true,
  //   delete: true,
  //   all: false
  // };



  useEffect(() => {
    setStateAccessList(parsedAccessList);
  }, [parsedAccessList]);

  // console.log("setStateAccessList", stateAccessList.Delegation.create);


  const teamTasksList = [
    {
      id: "1234",
      taskName: 'Market Research',
      status: "Inprogress",
      assignedDate: "27/08/2023",
      plannedDate: "30/08/3023",
      noOfRevision: "2",
      assignedTo: "Ruben",
      note: 'Lorem ipsum dolor sit amet.',
      file: "yes",
    },
    {
      id: "1234",
      taskName: 'Market Research',
      status: "Completed",
      assignedDate: "27/08/2023",
      plannedDate: "30/08/3023",
      noOfRevision: "2",
      assignedTo: "Ruben",
      note: 'Lorem ipsum dolor sit amet.',
      file: "no",
    },
    {
      id: "1234",
      taskName: 'Market Research',
      status: "Pending",
      assignedDate: "27/08/2023",
      plannedDate: "30/08/3023",
      noOfRevision: "2",
      assignedTo: "Ruben",
      note: 'Lorem ipsum dolor sit amet.',
      file: "yes",
    },
    {
      id: "1234",
      taskName: 'Market Research',
      status: "Moved",
      assignedDate: "27/08/2023",
      plannedDate: "30/08/3023",
      noOfRevision: "2",
      assignedTo: "Ruben",
      note: 'Lorem ipsum dolor sit amet.',
      file: "no",
    },
  ];

  const handleUpdateStatusClick = (task, title) => {
    setSelectedTask(task);
    setModalTitle(title)
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
  };

  return (
    <div className='manageTeamTasks'>
      <div className="manageTeamTasksTable" style={{ overflowX: 'auto' }}>
        <table>
          <thead>
            <tr>
              <th>Task Name</th>
              <th>Status</th>
              <th>Assigned Date</th>
              <th>Planned Date</th>
              <th>No of Revision</th>
              <th>Assigned To</th>
              <th>Note</th>
              <th>File</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {teamTasksList.map((task) => (
              <tr key={task.id}>
                <td>{task.taskName}</td>
                <td>{task.status}</td>
                <td>{task.assignedDate}</td>
                <td>{task.plannedDate}</td>
                <td>{task.noOfRevision}</td>
                <td>{task.assignedTo}</td>
                <td>{task.note}</td>
                <td>
                  <button className="tableBtn" style={{backgroundColor: task.file=== 'yes' ? "#1251F3" : "#bac2df"}}>
                    <img src={ImageList.EyeIcon} alt="" />
                    View File
                  </button>
                </td>
                <td className='actionBtns'>
                  <button className="tableBtn" style={{backgroundColor: '#FF833D'}} onClick={() => handleUpdateStatusClick(task, "Reassign")}>
                    <img src={ImageList.ReassignIcon} alt="" />
                    Reassign
                  </button>
                  <button className="tableBtn" onClick={() => handleUpdateStatusClick(task, "Task Transfer")}>
                    <img src={ImageList.TransferIcon} alt="" />
                    Transfer
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      {isModalOpen && (
        <TaskModal
          task={selectedTask}
          onClose={handleCloseModal}
          title={modalTitle}
        />
      )}
    </div>
  );
}

export default ManageTeamTasks;
