import React, { useState } from 'react';
import ImageList from '../../utils/ImageList';
import './ChecklistMyTasks.scss';
import TaskModal from '../TaskModal/TaskModal';

function ChecklistMyTasks() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedTask, setSelectedTask] = useState(null);
  const [modalTitle, setModalTitle] = useState("");

  // statusType 1 = Completed, 2 = Delayed, 3 = In Progress
  const myTaskList = [
    {
      id: "1234",
      taskName: 'Market Research',
      assignedDate: "27/08/2023",
      plannedDate: "30/08/3023",
      note: 'Lorem ipsum dolor sit amet.',
      noOfRevision: "2",
      status: "In Progress",
    },
    {
      id: "5678",
      taskName: 'Another Task',
      assignedDate: "28/08/2023",
      plannedDate: "31/08/3023",
      note: 'Another note.',
      noOfRevision: "2",
      status: "In Progress",
    },
    {
      id: "91011",
      taskName: 'Task in Progress',
      assignedDate: "29/08/2023",
      plannedDate: "01/09/3023",
      note: 'Task is in progress.',
      noOfRevision: "2",
      status: "In Progress",
    },
  ];



  const handleUpdateStatusClick = (task, title) => {
    setSelectedTask(task);
    setIsModalOpen(true);
    setModalTitle(title)
  };


  const handleCloseModal = () => {
    setIsModalOpen(false);
  };

  return (
    <div className='checklistMyTasks'>
      <div className="myTaskTable" style={{ overflowX: 'auto' }}>
        <table>
          <thead>
            <tr>
              <th>Task Name</th>
              <th>Status</th>
              <th>Assigned Date</th>
              <th>Planned Date</th>
              <th>No of Revision</th>
              <th>Note</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {myTaskList.map((task) => (
              <tr key={task.id} >
                <td>{task.taskName}</td>
                <td>{task.status}</td>
                <td>{task.assignedDate}</td>
                <td>{task.plannedDate}</td>
                <td>{task.noOfRevision}</td>
                <td>{task.note}</td>
                <td className='actionBtns'>
                  <button className="tableBtn" onClick={() => handleUpdateStatusClick(task, "Market Research")}>
                    <img src={ImageList.UpdateIcon} alt="" />
                    Update Status
                  </button>
                  <button className="tableBtn" style={{backgroundColor: "#F39212", color: "#000"}} onClick={() => handleUpdateStatusClick(task, "Notes")}>
                    <img src={ImageList.TaskIcon} alt="" />
                    Notes
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      {isModalOpen && (
        <TaskModal
          task={selectedTask}
          onClose={handleCloseModal}
          title = {modalTitle}
        />
      )}
    </div>
  );
}

export default ChecklistMyTasks;
