import { toast } from 'react-toastify';


export const showToasterMessage = (message) => {
    toast.error(message, {
        toastId: message,
        position: 'top-right',
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: 'colored',
      });
  };


export const showSuccessToast = (message) => {
    toast.success(message, {
        toastId: message,
        position: 'top-right',
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: 'colored',
    })
}