import React, { useEffect, useState } from "react";
import TopHeader from "../../components/TopHeader/TopHeader";
import SidebarNav from "../../components/SideBar/SideBar";
import SidebarMenuItems from "../../utils/SidebarMenuItems";
import "./Departments.scss";
import { Link } from "react-router-dom";
import { showToasterMessage } from "../../utils/helper";
import Cookies from "js-cookie";
import getDeptList from "../../interface/implementation/getDeptListImp";

function Departments() {
  const handleInputChange = () => {
    console.log("create");
  };

  const showToast = (errorText) => {
    showToasterMessage(errorText);
  };

  const [departInfo, setDepartInfo] = useState([
    {
      dept_id: "",
      dept_name: "",
      dept_head: "",
    },
  ]);

  useEffect(() => {
    const getAllDeptList = () => {
      const token = Cookies.get("token");
      getDeptList(token)
        .then((res) => {
          const mappedDepartments = res.result.map((department) => ({
            dept_id: department.dept_id || "",
            dept_name: department.dept_name || "",
            dept_head: department.dept_head || "",
          }));
          setDepartInfo(mappedDepartments);
          showToast(res);
        })
        .catch((error) => {
          console.log("error during getting deptList:", error);
          showToast("An error occurred during fetching All department List");
        });
    };
    getAllDeptList();
  }, []);

  return (
    <div className="departments">
      <TopHeader />
      <div className="dashboardBody">
        <SidebarNav menuItems={SidebarMenuItems} />
        <div className="dashboardContent">
          <div className="headerBody">
            <h2>Departments</h2>
            <div className="actionHeader">
              <div className="formInput">
                <div className="inputField">
                  <input
                    type="text"
                    id="completionDate"
                    onChange={handleInputChange}
                    value={""}
                    placeholder="Search...."
                  />
                </div>
              </div>
              <Link to={"/departments/create"} className="tableBtn">
                + Create New Department
              </Link>
            </div>
          </div>
          <div className="departmentsContainer">
            <div className="employeeDetails" style={{ overflowX: "auto" }}>
              <table className="employeeTable">
                <thead>
                  <tr>
                    <th>Department Name</th>
                    <th>Department Head</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {departInfo.map((form, index) => {
                    return (
                      <tr key={index}>
                        <td>{departInfo[index].dept_name}</td>
                        <td>{departInfo[index].dept_head}</td>
                        <td className="actionLink">
                          {" "}
                          {/* <Link to={"/departments/edit"}>Edit</Link> */}
                          <Link
                            to="/departments/edit"
                            state={{ editDept: departInfo[index] }}
                          >
                            Edit
                          </Link>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Departments;
