import createPermissionsAPI from "../api/createPermissionsAPI";

export default function createPermissions(body, token) {
  // console.log(accessToken);
  return createPermissionsAPI
      .post(body, token)
      .then((res) => {
        console.log("Response ", res);
        return res; 
    })
    .catch((err) => {
        console.log("Error", err);
        throw err; 
    });
  
}
