import React, { useState } from "react";
import "./Login.scss";
import { Link, useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import { showToasterMessage } from "../../utils/helper";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { login } from "../../interface/implementation/loginAPIImp";
import Cookies from "js-cookie";
import checkDept from "../../interface/implementation/checkDeptAPIImp";
import checkHoliday from "../../interface/implementation/checkEmployeeAPIImp";
import checkRole from "../../interface/implementation/checkRoleAPIImp";
import checkEmployee from "../../interface/implementation/checkEmployeeAPIImp";

function Login() {
  const params = useParams();
  const navigate = useNavigate();
  const [addDept, setAddDept] = useState(false);
  const [credentials, setCredentials] = useState({
    userEmail: "",
    userPass: "",
  });
  const [accessToken, setAccessToken] = useState('')

  const [showPassword, setShowPassword] = useState(false);
  const showToast = (errorText) => {
    showToasterMessage(errorText);
  };

  const handleInputChange = (e) => {
    const { id, value } = e.target;
    setCredentials((prevCredentials) => ({
      ...prevCredentials,
      [id]: value,
    }));
  };

  const handleTogglePassword = () => {
    setShowPassword((prevShowPassword) => !prevShowPassword);
  };



  const loginHandler = () => {
    if (!credentials.userEmail || !credentials.userPass) {
      // Email or password is missing
      console.log("Login failed: Email or password missing");
      showToast("Login failed: Email or password missing");
      return; // Stop execution if email or password is missing
    }

    if (!validateEmail(credentials.userEmail)) {
      // Email is invalid
      console.log("Login failed: Invalid email format");
      showToast("Login failed: Invalid email format");
      return; // Stop execution if email is invalid
    }

    if (!validatePassword(credentials.userPass)) {
      // Password is invalid
      console.log("Login failed: Invalid password format");
      showToast("Login failed: Password must be at least 6 characters long");
      return; // Stop execution if password is invalid
    }

    // Both email and password are present and valid, proceed with login
    let body = {
      company_uid: params.id,
      emp_email: credentials.userEmail,
      emp_password: credentials.userPass,
    };

   

    login(body)
      .then((res) => {
        // console.log(res);
        if (res.result === "user not found") {
          // User not found
          showToast("Wrong credentials");
        }
          console.log("res", res);
          setAccessToken(res.accessToken)
          Cookies.set("token", res.accessToken);
          checkDept(res.accessToken).then((res)=>{
                if(!res.result){
                  navigate("/new-department")
                }else{
                  checkRole(accessToken).then((res)=>{
                    if(!res.result){
                      navigate("/add-roles")
                    }else{
                      checkEmployee(accessToken).then((res)=>{
                        if(!res.result){
                          navigate("/add-employee")
                        }else{
                          checkHoliday(accessToken).then((res)=>{
                            if(!res.result){
                              navigate("/add-holiday")
                            }else{
                              navigate("/dashboard")
                            }
                      }).catch((err)=> console.log("err", err))
                        }
                  }).catch((err)=> console.log("err", err))
                    }
              }).catch((err)=> console.log("err", err))
                }
          }).catch((err)=> console.log("err", err))

          // navigate("/dashboard");
          
        
      })
      .catch((err) => {
        console.log("Error during login:", err);
        showToast("An error occurred during login");
      });
  };

  // Validation functions (assuming they are defined elsewhere in your code)
  function validateEmail(email) {
    // Regular expression to validate email format
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  }

  function validatePassword(password) {
    return password.length >= 6; // For example, minimum length of 6 characters
  }

  return (
    <div className="login">
      <ToastContainer limit={1} />
      <div className="loginContainer">
        <h2 className="title">LogIn</h2>
        <div className="loginForm">
          <div>
            <label htmlFor="userEmail">
              Username <span>*</span>
            </label>
            <div className="inputUserName">
              {/* <img src={ImageList.EmailIcon} alt="email-icon" className="emailIcon" /> */}
              <input
                type="text"
                id="userEmail"
                placeholder="Enter your username"
                onChange={handleInputChange}
                value={credentials.userEmail}
              />
            </div>
          </div>
          <div>
            <label htmlFor="userPass">
              Password <span>*</span>
            </label>
            <div className="inputPass">
              {/* <img src={ImageList.Password} alt="eye-icon" className="passIcon" /> */}
              <input
                type={showPassword ? "text" : "password"}
                id="userPass"
                placeholder="Password"
                onChange={handleInputChange}
                value={credentials.userPass}
              />
              {/* <img onClick={handleTogglePassword} src={showPassword ? ImageList.EyeIcon : ImageList.EyeClose} alt="eye-icon" className="eyeIcon" /> */}
            </div>
            <p className="bottomText">
              Enter the password that accompanies your username.
            </p>
          </div>
        </div>

        <p className="bottomText">
          Don't attempt login over 5 times. If you have trouble logging in,
          please reset your password.
        </p>

        <div className="actionBtns">
          <button className="loginBtn" onClick={loginHandler}>
            Log in
            {/* <img src={ImageList.ArrowForward} alt="" className="forwardArrow" /> */}
          </button>
          <Link to={"#"} className="resetPass">
            Forgot Your password?
          </Link>
        </div>
      </div>
    </div>
  );
}

export default Login;
