import newRoleAPI from "../api/newRoleAPI";

export default function newRole(body, token) {
  // console.log(accessToken);
  return newRoleAPI
      .post(body, token)
      .then((res) => {
        console.log("Response ", res);
        return res; 
    })
    .catch((err) => {
        console.log("Error", err);
        throw err; 
    });
  
}
