import React, { useState, useEffect } from "react";
import TopHeader from "../../components/TopHeader/TopHeader";
import SidebarNav from "../../components/SideBar/SideBar";
import SidebarMenuItems from "../../utils/SidebarMenuItems";
import "./Holiday.scss";
import { Link } from "react-router-dom";
import ImageList from "../../utils/ImageList";
import { showToasterMessage } from "../../utils/helper";
import Cookies from "js-cookie";
import getHolidayList from "../../interface/implementation/getHolidayListImp";

function Holiday() {
  const handleInputChange = () => {
    console.log("create");
  };

  const showToast = (errorText) => {
    showToasterMessage(errorText);
  };

  const [holidayInfo, setholidayInfo] = useState([
    {
      holiday_id: "",
      holiday_name: "",
      holiday_start_date: "",
      holiday_end_date: "",
    },
  ]);

  useEffect(() => {
    const getAllHolidayList = () => {
      const token = Cookies.get("token");
      getHolidayList(token)
        .then((res) => {
          const mappedHolidays = res.result.map((holiday) => ({
            holiday_id: holiday.holiday_id,
            holiday_name: holiday.holiday_name,
            holiday_start_date: new Date(holiday.holiday_start_date)
              .toISOString()
              .split("T")[0],
            holiday_end_date: new Date(holiday.holiday_end_date)
              .toISOString()
              .split("T")[0],
          }));
          setholidayInfo(mappedHolidays);
          showToast(res);
          console.log(res);
        })
        .catch((error) => {
          console.log("error during getting deptList:", error);
          showToast("An error occurred during fetching All department List");
        });
    };
    getAllHolidayList();
  }, []);

  return (
    <div className="holiday">
      <TopHeader />
      <div className="dashboardBody">
        <SidebarNav menuItems={SidebarMenuItems} />
        <div className="dashboardContent">
          <div className="headerBody">
            <h2>Holiday list</h2>
            <div className="actionHeader">
              <div className="formInput">
                <div className="inputField">
                  <input
                    type="text"
                    id="completionDate"
                    onChange={handleInputChange}
                    value={""}
                    placeholder="Search..."
                  />
                </div>
              </div>
              <Link to={"/holiday-list/create"} className="tableBtn">
                + Add New Holiday List
              </Link>
            </div>
          </div>
          <div className="headerInputs">
            <div className="filterContainer">
              <div className="formInput">
                <label htmlFor="name">Non Working Days</label>
                <div className="inputField">
                  <input
                    type="text"
                    id="name"
                    onChange={handleInputChange}
                    value={""}
                    placeholder="Search and select"
                  />
                </div>
              </div>
              <div className="filters">
                <p className="filterItem">
                  Project Manager
                  <img src={ImageList.CrossBlue} alt="" />
                </p>
                <p className="filterItem">
                  Project Manager
                  <img src={ImageList.CrossBlue} alt="" />
                </p>
              </div>
            </div>
            <div className="filterContainer">
              <div className="formInput">
                <label htmlFor="name">First Working Day</label>
                <div className="inputField">
                  <select
                    id="workingDays"
                    onChange={handleInputChange}
                    value={""}
                  >
                    <option value="">Sunday</option>
                    <option value="">Monday</option>
                    <option value="">Tuesday</option>
                    <option value="">Wednesday</option>
                    <option value="">Thursday</option>
                    <option value="">Friday</option>
                    <option value="">Saturday</option>
                  </select>
                </div>
              </div>
              <div className="filters">
                <p className="filterItem">
                  Project Manager
                  <img src={ImageList.CrossBlue} alt="" />
                </p>
                <p className="filterItem">
                  Project Manager
                  <img src={ImageList.CrossBlue} alt="" />
                </p>
              </div>
            </div>
            <div className="filterContainer">
              <div className="formInput">
                <label htmlFor="name">Starting Week</label>
                <div className="inputField">
                  <input
                    type="date"
                    id="name"
                    onChange={handleInputChange}
                    value={""}
                    placeholder="Search and select"
                  />
                </div>
              </div>
              <div className="filters">
                <p className="filterItem">
                  Project Manager
                  <img src={ImageList.CrossBlue} alt="" />
                </p>
                <p className="filterItem">
                  Project Manager
                  <img src={ImageList.CrossBlue} alt="" />
                </p>
              </div>
            </div>
          </div>
          <div className="holidayContainer">
            <div className="employeeDetails" style={{ overflowX: "auto" }}>
              <table className="employeeTable">
                <thead>
                  <tr>
                    <th>Holiday Name</th>
                    <th>Start Date</th>
                    <th>End Date</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {holidayInfo.map((form, index) => {
                    return (
                      <tr>
                        <td>{holidayInfo[index].holiday_name}</td>
                        <td>{holidayInfo[index].holiday_start_date}</td>
                        <td>{holidayInfo[index].holiday_end_date}</td>
                        <td className="actionLink">
                          {" "}
                          {/* <Link to={"/holiday-list/edit"}>Edit</Link> */}
                          <Link
                            to="/holiday-list/edit"
                            state={{ editHoliday: holidayInfo[index] }}
                          >
                            Edit
                          </Link>
                        </td>
                      </tr>
                    );
                  })}
                  {/* <tr>
                    <td>Republic Day</td>
                    <td>26 Jan, 2024</td>
                    <td>26 Jan, 2024</td>
                    <td className="actionLink">
                      {" "}
                      <Link to={"/holiday-list/edit"}>Edit</Link>
                    </td>
                  </tr> */}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Holiday;
