import React, { useState } from 'react';
import TopHeader from '../../components/TopHeader/TopHeader';
import SidebarNav from '../../components/SideBar/SideBar';
import SidebarMenuItems from '../../utils/SidebarMenuItems';
import ImageList from '../../utils/ImageList';
import './AdminAllTasks.scss';
import TaskModal from '../../components/TaskModal/TaskModal';

import ChecklistMyTasks from '../../components/ChecklistMyTasks/ChecklistMyTasks';
import MyTasks from '../../components/MyTasks/MyTasks';

function AdminAllTasks() {
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [selectedTask, setSelectedTask] = useState(null);
    const [activeItemId, setActiveItemId] = useState(1);


    // statusType 1 = Completed, 2 = Delayed, 3 = In Progress
    const myTaskList = [
        {
            id: "1234",
            taskName: 'Market Research',
            assignedDate: "27/08/2023",
            plannedDate: "30/08/3023",
            note: 'Lorem ipsum dolor sit amet.',
            noOfRevision: "2",
            status: 1,
        },
        {
            id: "5678",
            taskName: 'Another Task',
            assignedDate: "28/08/2023",
            plannedDate: "31/08/3023",
            note: 'Another note.',
            noOfRevision: "2",
            status: 2,
        },
        {
            id: "91011",
            taskName: 'Task in Progress',
            assignedDate: "29/08/2023",
            plannedDate: "01/09/3023",
            note: 'Task is in progress.',
            noOfRevision: "2",
            status: 3,
        },
    ];

    const handleUpdateStatusClick = (task) => {
        setSelectedTask(task);
        setIsModalOpen(true);
    };

    const handleCloseModal = () => {
        setIsModalOpen(false);
    };
    const handleInputChange = () => {
        console.log("click")
    };

    const handleBodyNavigationClick = (itemId) => {
        setActiveItemId(itemId);
    };

    const bodyNavigationItems = [
        { id: 1, name: 'Checklist' },
        { id: 2, name: 'Delegation' },
    ];


    return (
        <div className='adminAllTasks'>
            <TopHeader />
            <div className="dashboardBody">
                <SidebarNav menuItems={SidebarMenuItems} />
                <div className="dashboardContent">
                    <div className="headerBody">
                        <h2>All Tasks</h2>
                        <div className="actionHeader">
                            <div className='formInput'>
                                <div className="inputField">
                                    <input
                                        type="text"
                                        id="completionDate"
                                        onChange={handleInputChange}
                                        value={""}
                                        placeholder='Search...'
                                    />
                                </div>
                            </div>
                            {/* <Link to={'/holiday/create'} className="tableBtn">+ Add New Holiday List</Link> */}
                        </div>
                    </div>
                    <div className="allTaskContainer">
                        <div className="bodyNavigation">
                            {bodyNavigationItems.map((item) => (
                                <p
                                    key={item.id}
                                    className={`title ${activeItemId === item.id ? 'active' : ''}`}
                                    onClick={() => handleBodyNavigationClick(item.id)}
                                >
                                    {item.name}
                                </p>
                            ))}
                        </div>

                        {activeItemId === 1 && <ChecklistMyTasks />}
                        {activeItemId === 2 && <MyTasks />}
                    </div>
                </div>

            </div>


            {isModalOpen && (
                <TaskModal
                    task={selectedTask}
                    onClose={handleCloseModal}
                    title={"Market Research"}
                />
            )}
        </div>
    );
}

export default AdminAllTasks;
