import autoCompleteRoleAPI from "../api/autoCompleteRoleAPI";

export default function autoCompleteRole(body, token) {
  // console.log(accessToken);
  return autoCompleteRoleAPI
      .post(body, token)
      .then((res) => {
        console.log("Response ", res);
        return res; 
    })
    .catch((err) => {
        console.log("Error", err);
        throw err; 
    });
  
}
