import React, { useState } from 'react';
import TopHeader from '../../components/TopHeader/TopHeader';
import SidebarNav from '../../components/SideBar/SideBar';
import SidebarMenuItems from '../../utils/SidebarMenuItems';
import './CreateProcessChecklist.scss';
import ImageList from '../../utils/ImageList';


function CreateProcessChecklist() {
  const [taskInfo, setTaskInfo] = useState({
    title: '',
    assignTo: '',
    completionDate: '',
  });

  const [steps, setSteps] = useState([
    {
      title: '',
      assignTo: '',
      completionDate: '',
      frequency: '',
      endDate: '',
    },
  ]);

  const handleInputChange = (e, stepIndex) => {
    const { id, value } = e.target;
    setSteps((prevSteps) => {
      const newSteps = [...prevSteps];
      newSteps[stepIndex] = {
        ...newSteps[stepIndex],
        [id]: value,
      };
      return newSteps;
    });
  };

  const handleSaveButtonClick = () => {
  };

  const handleBodyNavigationClick = (itemId) => {
  };

  const handleAddStep = () => {
    setSteps((prevSteps) => [...prevSteps, {}]);
  };

  const handleDeleteStep = (stepIndex) => {
    setSteps((prevSteps) => prevSteps.filter((_, index) => index !== stepIndex));
  };

  return (
    <div className="createProcessChecklist">
      <TopHeader />
      <div className="dashboardBody">
        <SidebarNav menuItems={SidebarMenuItems} />
        <div className="dashboardContent">
          <div className="headerBody">
            <p className="title">Create Process Checklist</p>
            <button className="tableBtn">Save</button>
          </div>
          <div className="searchContainer">
            <div className="formFields">
              <div className='formInput'>
                <label htmlFor="title">Process Name </label>
                <div className="inputField">
                  <input
                    type="text"
                    id="title"
                    onChange={handleInputChange}
                    value={taskInfo.title}
                    placeholder='Enter Process Name'
                  />
                </div>
              </div>

            </div>
          </div>
          <div className='createProcessChecklistContainer'>

            {steps.map((step, index) => (
              <div className="processStep" key={index}>
                <div className="header">
                  <p className='heading'>{`Step ${index + 1}`}</p>
                  <div className="actionBtns">
                    {
                      index === 0 && (
                        <button className="tableBtn" onClick={handleAddStep}>+ Add New Step</button>
                      )
                    }
                    {
                      steps.length > 1 && (
                        <button className="tableBtn" style={{ backgroundColor: '#D80032' }} onClick={() => handleDeleteStep(index)}><img src={ImageList.DeleteWhiteIcon} alt="" />
                          Delete</button>
                      )
                    }
                  </div>
                </div>
                <div className="formFields">
                  <div className='formInput'>
                    <label htmlFor="title">Task</label>
                    <div className="inputField">
                      <input
                        type="text"
                        id="title"
                        onChange={handleInputChange}
                        value={taskInfo.title}
                        placeholder='Enter title'
                      />
                    </div>
                  </div>

                  <div className='formInput' >
                    <label htmlFor="assignTo">Assigned to </label>
                    <div className="inputField">
                      <select
                        id="assignTo"
                        onChange={handleInputChange}
                        value={taskInfo.assignTo}
                      >
                        <option value="" disabled>Select assignee</option>
                        <option value="assignee1">Assignee 1</option>
                        <option value="assignee2">Assignee 2</option>
                      </select>
                    </div>
                  </div>

                  <div className='formInput'>
                    <label htmlFor="completionDate">Planned date </label>
                    <div className="inputField">
                      <input
                        type="date"
                        id="completionDate"
                        onChange={handleInputChange}
                        value={taskInfo.completionDate}
                        placeholder='Enter completion date'
                      />
                    </div>
                  </div>
                  <div className='formInput' >
                    <label htmlFor="assignTo">Frequency </label>
                    <div className="inputField">
                      <select
                        id="assignTo"
                        onChange={handleInputChange}
                        value={taskInfo.assignTo}
                      >
                        <option value="" disabled>Select Frequency</option>
                        <option value="assignee1">Assignee 1</option>
                        <option value="assignee2">Assignee 2</option>
                      </select>
                    </div>
                  </div>
                  <div className='formInput'>
                    <label htmlFor="completionDate">End date </label>
                    <div className="inputField">
                      <input
                        type="date"
                        id="completionDate"
                        onChange={handleInputChange}
                        value={taskInfo.completionDate}
                        placeholder='Enter completion date'
                      />
                    </div>
                  </div>

                </div>
              </div>
            ))}
            <button className="tableBtn" onClick={handleAddStep}>+ Add New Step</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CreateProcessChecklist;
