import nonWorkingDayAPI from "../api/nonWorkingDayAPI";

export default function createNonWorkingDay(body, token) {
  return nonWorkingDayAPI
      .post(body, token)
      .then((res) => {
        console.log("Response ", res);
        return res; 
    })
    .catch((err) => {
        console.log("Error", err);
        throw err; 
    });
  
}
