import getHolidayAPI from "../api/getHolidayListAPI";

export default function getHoliday(token) {
  return getHolidayAPI
      .get(token)
      .then((res) => {
        console.log("Response ", res);
        return res; 
    })
    .catch((err) => {
        console.log("Error", err);
        throw err; 
    });
  
}
