import newEmpAPI from "../api/newEmpAPI";

export default function newEmp(body, token) {
  return newEmpAPI
    .post(body, token)
    .then((res) => {
      console.log("Response ", res);
      return res;
    })
    .catch((err) => {
      console.log("Error", err);
      throw err;
    });
}
