import React, { useState, useEffect } from "react";
import "./SideBar.scss";
import ImageList from "../../utils/ImageList";
import { useNavigate, useLocation } from "react-router-dom";
import { userEA } from "../../utils/SidebarMenuItems";
import { AdminMenus, AllTaskMenu  } from "../../utils/SidebarMenuItems";

function SidebarNav() {
  // const allRoleList = [
  //   { value: "1", name: "Student" },
  //   { value: "2", name: "Super Admin" },
  //   { value: "3", name: "Sub Admin" },
  //   { value: "4", name: "Batch CRM" },
  //   { value: "5", name: "Admin CRM" },
  // ];
  // const [userRoles, setUserRoles] = useState([]);
  const [activeList, setActiveList] = useState([]);
  const [stateAccessList, setStateAccessList] = useState([]);
  const [dropdownList, setDropdownList] = useState([]);
  const [selectedRole, setSelectedRole] = useState("");
  const [userRoleName, setUserRoleName] = useState("");
  // const [ user, setUser] = useState({});
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    const userRole = localStorage.getItem("userRole");
    const storedAccessList = localStorage.getItem("accessList");


    console.log("storedAccessList", storedAccessList);


    if (!userRole) {
      localStorage.setItem("userRole", "admin");
    }
    // Retrieve and parse accessList from local storage
    // const accessList = storedAccessList ? JSON.parse(storedAccessList) : null;
    const tempArray = storedAccessList ? JSON.parse(storedAccessList) : null;
    const accessList = { ...tempArray }
    // accessList.Delegation = {
    //   create: false,
    //   view: false,
    //   update: true,
    //   delete: true,
    //   all: false
    // };
    
    console.log ("accessList", accessList);

    // const accessList = storedAccessList ? JSON.parse(storedAccessList) : null;

    if (accessList && (accessList.Delegation || accessList.Checklist)) {
      const hideDelegation = accessList.Delegation && Object.values(accessList.Delegation).every(value => !value);

      const hideChecklist = accessList.Checklist && Object.values(accessList.Checklist).every(value => !value);

  
      setStateAccessList(accessList);
      // Filter out menus based on accessList
      const filteredMenus = AdminMenus.filter(({ title }) => {
        const menuKey = title.toLowerCase().replace(/\s+/g, "");
        return !((menuKey === "delegation" && hideDelegation) || (menuKey === "checklist" && hideChecklist));
      });


      setActiveList(filteredMenus);
    } else {
      setActiveList(AllTaskMenu);
    }
    // if (userRole === "admin") {
    //   setActiveList(AdminMenus);
    // }
    // setActiveList(AdminMenus);
    setUserRoleName(userRole || "admin");
  }, []);



  const signOut = () => {
    localStorage.removeItem("selectedRole");
  };

  const handleRoleChange = (event) => {
    const selectedValue = event.target.value;
    setSelectedRole(selectedValue);
    localStorage.setItem("selectedRole", selectedValue);
  };


  const handleMenuClick = (index, link) => {
    const hasSubmenus = activeList[index]?.subMenus?.length > 0;
    console.log("hasSubmenus", hasSubmenus);
    const isActive =
      location.pathname === link || location.pathname.includes(link);

    if (hasSubmenus) {
      setActiveList((prev) =>
        prev.map((item, i) => (i === index ? { ...item, active: !item.active } : item))
      );
    } else if (!isActive) {
      navigate(link);
    }
  };


  const handleSubMenuClick = (link) => {
    navigate(link);
  };

  return (
    <div className="sideBarContainer">
      <div className="headerDropdown">
        <select
          id="userRole"
          onChange={handleRoleChange}
          value={selectedRole}
        >
          <option value="">{userRoleName}</option>
        </select>
      </div>
      <div className="menuItemList">
        {activeList.map(({ link, iconSrc, title, subMenus }, index) => (
          <div className="menuItem" key={index}>
            <div
              className={`menuBox ${(location.pathname === link || location.pathname.includes(link)) ? "active" : ""
                }`}
              onClick={() => handleMenuClick(index, link)}
            >
              <div className="iconText">
                <img src={iconSrc} alt="" className="menuIcon" />
                <p className="menuTitle">{title}</p>
              </div>
              {subMenus && subMenus.length > 0 && (
                <img
                  src={ImageList.DropdownIcon}
                  alt=""
                  className="dropdownIndicator"
                />
              )}
            </div>
            {subMenus && subMenus.length > 0 && (
        <div
          className="subMenus"
          style={{
            display: activeList[index]?.active ? "block" : "none",
          }}
        >
          {subMenus.map(({ link, title }, subIndex) => (
            !((title === "Create Task" && stateAccessList.Delegation.create === false)) && (
              <p
                className={`menuBox ${(location.pathname === link || location.pathname.includes(link)) ? "active" : ""
                  }`}
                onClick={() => handleSubMenuClick(link)}
                key={subIndex}
              >
                {title}
              </p>
            )
          ))}
        </div>
      )}

          </div>
        ))}
      </div>

      <div className="sidebarFooter">
        <div className="avatarContainer">
          {/* <div className="avatar">
            <img src={user.profile_picture ? user.profile_picture :  ImageList.UserSiderbarImg} alt="" />
            <span className="greenDot"></span>
          </div> */}
          <div className="details">
            <p className="name">xyz </p>
            <p className="number">xyz@gmail.com</p>
          </div>
        </div>
        <div className="logoutBox" onClick={signOut}>
          <img src={ImageList.LogoutIcon} alt="" />
          <p className="logout">Logout</p>
        </div>
      </div>
    </div>
  );
}

export default SidebarNav;
