import React, { useState } from "react";
import TopHeader from "../../components/TopHeader/TopHeader";
import ImageList from "../../utils/ImageList";
import "./NewDepartment.scss";
import { useNavigate } from "react-router-dom";
import { showToasterMessage } from "../../utils/helper";
import newDept from "../../interface/implementation/newDeptImp";
import Cookies from "js-cookie";

function NewDepartment() {
  const navigate = useNavigate();

  const showToast = (errorText) => {
    showToasterMessage(errorText);
  };

  const [department, setDepartment] = useState([
    {
      dept_name: "",
      dept_head: "",
    },
  ]);

  const handleInputChange = (e, index) => {
    let data = [...department];
    data[index][e.target.id] = e.target.value;
    setDepartment(data);
  };

  // submit
  const handleNextButtonClick = () => {
    if (department[0].dept_name.length <= 0) {
      showToast("Please enter atleast one Department");
    } else {
      let body = [...department];

      const token = Cookies.get("token");

      newDept(body, token)
        .then((res) => {
          showToast(res);
          navigate("/add-roles");
        })
        .catch((error) => {
          console.log("error during newDept post:", error);
          showToast("An error occurred during new Department creation");
        });
    }
  };

  const addFields = () => {
    let object = {
      dept_name: "",
      dept_head: "",
    };
    setDepartment([...department, object]);
  };

  const removeFields = (index) => {
    let data = [...department];
    data.splice(index, 1);
    setDepartment(data);
  };

  return (
    <div className="newDepartment">
      <TopHeader />
      <div className="newDepartmentBody">
        {/* <h2>Create New Department</h2> */}
        <div className="topNavigation">
          <p className="item active">Department</p>
          <p className="item">Roles</p>
          <p className="item">Employees</p>
          <p className="item">Holiday List</p>
          <p className="item">Permissions</p>
        </div>

        <div className="newDepartmentContainer">
          <h2>Create Department</h2>

          {department.map((form, index) => {
            return (
              <div className="departmentContent" key={index}>
                <div className="formFields">
                  <div className="formInput">
                    <label htmlFor="departmentName">
                      Name of the Department *{" "}
                    </label>
                    <div className="inputField">
                      <input
                        id="dept_name"
                        value={department[index].dept_name}
                        onChange={(e) => handleInputChange(e, index)}
                        placeholder="Enter department name"
                      />
                    </div>
                  </div>
                  <div className="formInput">
                    <label htmlFor="departmentSize">
                      Department Head (Optional)
                    </label>
                    <div className="inputField">
                      <input
                        id="dept_head"
                        value={department[index].dept_head}
                        onChange={(e) => handleInputChange(e, index)}
                        placeholder="Enter department size"
                      />
                    </div>
                  </div>
                </div>
                <div className="formActions">
                  <p className="add" onClick={addFields}>
                    <img src={ImageList.AddBlueIcon} alt="" /> Add new
                    department
                  </p>
                  <p className="delete" onClick={() => removeFields(index)}>
                    <img src={ImageList.DeleteRedIcon} alt="" /> Delete
                  </p>
                </div>
              </div>
            );
          })}

          <button onClick={handleNextButtonClick} className="nextBtn">
            Next
          </button>
        </div>
      </div>
    </div>
  );
}

export default NewDepartment;
