import checkEmployeeAPI from "../api/checkEmployeeAPI";

export default function checkEmployee(token) {
  // console.log(accessToken);
  return checkEmployeeAPI
      .get(token)
      .then((res) => {
        console.log("Response ", res);
        return res; 
    })
    .catch((err) => {
        console.log("Error", err);
        throw err; 
    });
  
}
