import React, { useState } from 'react';
import TopHeader from '../../components/TopHeader/TopHeader';
import SidebarNav from '../../components/SideBar/SideBar';
import SidebarMenuItems from '../../utils/SidebarMenuItems';
import './DoerChecklist.scss';
import { Link } from 'react-router-dom';

import ChecklistMyTasks from '../../components/ChecklistMyTasks/ChecklistMyTasks';
import ChecklistTeamTasks from '../../components/ChecklistTeamTasks/ChecklistTeamTasks';
import ChecklistAllTeamTasks from '../../components/ChecklistAllTeamTasks/ChecklistAllTeamTasks';

function DoerChecklist() {
  const [taskInfo, setTaskInfo] = useState({
    title: '',
    assignTo: '',
    completionDate: '',
  });
  const [activeItemId, setActiveItemId] = useState(1);


  const handleInputChange = (e) => {
    const { id, value } = e.target;
    setTaskInfo((prevTaskInfo) => ({
      ...prevTaskInfo,
      [id]: value,
    }));
  };

  const handleSaveButtonClick = () => {
  };

  const handleBodyNavigationClick = (itemId) => {
    setActiveItemId(itemId);
  };

  const bodyNavigationItems = [
    { id: 1, name: 'My Tasks' },
    { id: 2, name: 'Manage Team Tasks' },
    { id: 3, name: 'All Team Tasks' },
  ];

  return (
    <div className="doerChecklist">
      <TopHeader />
      <div className="dashboardBody">
        <SidebarNav menuItems={SidebarMenuItems} />
        <div className="dashboardContent">
          <div className="headerBody">
            <p className="title">{activeItemId === 1 ? "Tasks" : "Process Checklist"}</p>
            <Link to={'/doer-checklist/create-task'} className="tableBtn">+ Create Task</Link>
          </div>
          <div className="searchContainer">
            <div className="formFields">
              <div className='formInput'>
                <label htmlFor="title">Search </label>
                <div className="inputField">
                  <input
                    type="text"
                    id="title"
                    onChange={handleInputChange}
                    value={taskInfo.title}
                    placeholder='Enter title'
                  />
                </div>
              </div>
              {
                activeItemId === 1 && (
                  <div className='formInput' >
                  <label htmlFor="assignTo">Status </label>
                  <div className="inputField">
                    <select
                      id="assignTo"
                      onChange={handleInputChange}
                      value={taskInfo.assignTo}
                    >
                      <option value="" disabled>Select assignee</option>
                      <option value="assignee1">Assignee 1</option>
                      <option value="assignee2">Assignee 2</option>
                    </select>
                  </div>
                </div>
                )
              }
              {
                (activeItemId === 2 || activeItemId === 3) &&(
                  <div className='formInput' >
                  <label htmlFor="assignTo">Assigned to </label>
                  <div className="inputField">
                    <select
                      id="assignTo"
                      onChange={handleInputChange}
                      value={taskInfo.assignTo}
                    >
                      <option value="" disabled>Select assignee</option>
                      <option value="assignee1">Assignee 1</option>
                      <option value="assignee2">Assignee 2</option>
                    </select>
                  </div>
                </div>
                )
              }
              {
                (activeItemId === 2 || activeItemId === 3) &&(
                  <div className='formInput' >
                  <label htmlFor="assignTo">Process</label>
                  <div className="inputField">
                    <select
                      id="assignTo"
                      onChange={handleInputChange}
                      value={taskInfo.assignTo}
                    >
                      <option value="" disabled>Select assignee</option>
                      <option value="assignee1">Assignee 1</option>
                      <option value="assignee2">Assignee 2</option>
                    </select>
                  </div>
                </div>
                )
              }

              <div className='formInput'>
                <label htmlFor="completionDate">Select Week </label>
                <div className="inputField">
                  <input
                    type="week"
                    id="completionDate"
                    onChange={handleInputChange}
                    value={taskInfo.completionDate}
                    placeholder='Enter completion date'
                  />
                </div>
              </div>
              <div className='formInput'>
                <label htmlFor="completionDate">Date </label>
                <div className="inputField">
                  <input
                    type="date"
                    id="completionDate"
                    onChange={handleInputChange}
                    value={taskInfo.completionDate}
                    placeholder='Enter completion date'
                  />
                </div>
              </div>
            <div className='searchBtns'>
              <button onClick={handleSaveButtonClick} className="proceedBtn">Apply</button>
              <button onClick={handleSaveButtonClick} className="proceedBtn clearBtn">Clear All</button>
            </div>
            </div>
          </div>
        <div className='doerChecklistContainer'>
        <div className="bodyNavigation">
            {bodyNavigationItems.map((item) => (
              <p
                key={item.id}
                className={`title ${activeItemId === item.id ? 'active' : ''}`}
                onClick={() => handleBodyNavigationClick(item.id)}
              >
                {item.name}
              </p>
            ))}
          </div>

          {activeItemId === 1 && <ChecklistMyTasks />}
          {activeItemId === 2 && <ChecklistTeamTasks />}
          {activeItemId === 3 && <ChecklistAllTeamTasks />}
        </div>
        </div>
      </div>
    </div>
  );
}

export default DoerChecklist;
