import checkDeptAPI from "../api/checkDeptAPI";

export default function checkDept(token) {
  // console.log(accessToken);
  return checkDeptAPI
      .get(token)
      .then((res) => {
        console.log("Response ", res);
        return res; 
    })
    .catch((err) => {
        console.log("Error", err);
        throw err; 
    });
  
}
