import { apiProvider } from "./apiProvider";

export class ApiCore {
  constructor(options) {
    // console.log("options::", options);

    if (options.post) {
      this.post = (body, token) => {
        return new Promise((resolve, reject) => {
          apiProvider
            .post(options.url, body, token)
            .then(resolve) // resolve with the response
            .catch(reject); // reject with the error
        });
      };
    }
    if (options.get) {
      this.get = (token) => {
        // console.log("here", token);
        return new Promise((resolve, reject) => {
          apiProvider.get(options.url, token)
              .then(resolve)  // resolve with the response
              .catch(reject); // reject with the error
      });
      };
    }

    if (options.postFormData) {
      this.postFormData = (body, token) => {
        return new Promise((resolve, reject) => {
          apiProvider
            .postFormData(options.url, body, token)
            .then(resolve) // resolve with the response
            .catch(reject); // reject with the error
        });
      };
    }

    if (options.put) {
      this.put = (body, token) => {
        return new Promise((resolve, reject) => {
          apiProvider
            .put(options.url, body, token)
            .then(resolve) // resolve with the response
            .catch(reject); // reject with the error
        });
      };
    }
  }
}
