import React, { useState } from "react";
import TopHeader from "../../components/TopHeader/TopHeader";
// import ImageList from '../../utils/ImageList';
import "./PermissionManagement.scss";
import { useNavigate } from "react-router-dom";
import Delegation from "../../components/Delegation/Delegation";
import Checklist from "../../components/Checklist/Checklist";
import UserManagement from "../../components/UserManagement/UserManagement";

function PermissionManagement() {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    documentFiles: [],
  });

  const [activeItemId, setActiveItemId] = useState(1); // Default active item id

  const handleBodyNavigationClick = (itemId) => {
    setActiveItemId(itemId);
  };

  const bodyNavigationItems = [
    { id: 1, name: "Delegation" },
    { id: 2, name: "Checklist" },
    { id: 3, name: "User Management" },
  ];

  const donexttask = (tabId) => {
    if(tabId === 4){
       navigate('/dashboard')
    }else{
    setActiveItemId(tabId); //prevstate+1
    } 
  };

  return (
    <div className="permissionManagement">
      <TopHeader />
      <div className="permissionManagementBody">
        {/* <h2>Permissions Management</h2> */}
        <div className="topNavigation">
          <p className="item active">Department</p>
          <p className="item active">Roles</p>
          <p className="item active">Employees</p>
          <p className="item active">Holiday List</p>
          <p className="item active">Permissions</p>
        </div>

        <div className="permissionManagementContainer">
          <h2>Permissions Management</h2>
          <div className="bodyNavigation">
            {bodyNavigationItems.map((item) => (
              <p
                key={item.id}
                className={`title ${activeItemId === item.id ? "active" : ""}`}
                onClick={() => handleBodyNavigationClick(item.id)}
              >
                {item.name}
              </p>
            ))}
          </div>

          {activeItemId === 1 && <Delegation submissionDone={donexttask} />}
          {activeItemId === 2 && <Checklist submissionDone={donexttask}  />}
          {activeItemId === 3 && <UserManagement submissionDone={donexttask} />}
        </div>
      </div>
    </div>
  );
}

export default PermissionManagement;
