import uploadHolidayCSVAPI from "../api/uploadHolidayCSVAPI";

export function uploadHolidayCSV(body, token) {
  return uploadHolidayCSVAPI
      .postFormData(body, token)
      .then((res) => {
        console.log("Response ", res);
        return res; 
    })
    .catch((err) => {
        console.log("Error", err);
        throw err; 
    });
}
