import React, { useState } from 'react';
import ImageList from '../../utils/ImageList';
import './MyTasks.scss';
import TaskModal from '../TaskModal/TaskModal';

function MyTasks() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedTask, setSelectedTask] = useState(null);

  // statusType 1 = Completed, 2 = Delayed, 3 = In Progress
  const myTaskList = [
    {
      id: "1234",
      taskName: 'Market Research',
      assignedDate: "27/08/2023",
      plannedDate: "30/08/3023",
      note: 'Lorem ipsum dolor sit amet.',
      noOfRevision: "2",
      status: 1,
    },
    {
      id: "5678",
      taskName: 'Another Task',
      assignedDate: "28/08/2023",
      plannedDate: "31/08/3023",
      note: 'Another note.',
      noOfRevision: "2",
      status: 2,
    },
    {
      id: "91011",
      taskName: 'Task in Progress',
      assignedDate: "29/08/2023",
      plannedDate: "01/09/3023",
      note: 'Task is in progress.',
      noOfRevision: "2",
      status: 3,
    },
  ];

  const getRowColor = (status) => {
    switch (status) {
      case 1:
        return '#3FCF7E';
      case 2:
        return '#D4212C96';
      case 3:
        return '#EAC07B';
      default:
        return '#fff';
    }
  };

  const handleUpdateStatusClick = (task) => {
    setSelectedTask(task);
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
  };

  return (
    <div className='myTasks'>
      <div className="myTaskTable" style={{ overflowX: 'auto' }}>
        <table>
          <thead>
            <tr>
              <th>Task Name</th>
              <th>Assigned Date</th>
              <th>Planned Date</th>
              <th>No of Revision</th>
              <th>Note</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {myTaskList.map((task) => (
              <tr key={task.id} style={{ backgroundColor: getRowColor(task.status) }}>
                <td>{task.taskName}</td>
                <td>{task.assignedDate}</td>
                <td>{task.plannedDate}</td>
                <td>{task.noOfRevision}</td>
                <td>{task.note}</td>
                <td>
                  <button className="tableBtn" onClick={() => handleUpdateStatusClick(task)}>
                    <img src={ImageList.UpdateIcon} alt="" />
                    Update Status
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      {isModalOpen && (
        <TaskModal
          task={selectedTask}
          onClose={handleCloseModal}
          title = {"Market Research"}
        />
      )}
    </div>
  );
}

export default MyTasks;
