import React, { useState, useEffect } from "react";
import TopHeader from "../../components/TopHeader/TopHeader";
import SidebarNav from "../../components/SideBar/SideBar";
import SidebarMenuItems from "../../utils/SidebarMenuItems";
import "./Roles.scss";
import { Link } from "react-router-dom";
import { showToasterMessage } from "../../utils/helper";
import Cookies from "js-cookie";
import getroleList from "../../interface/implementation/getRoleListImp";

function Roles() {
  const handleInputChange = () => {
    console.log("create");
  };

  const showToast = (errorText) => {
    showToasterMessage(errorText);
  };

  const [roleInfo, setRoleInfo] = useState([
    {
      role_name: "",
      role_id: "",
    },
  ]);

  useEffect(() => {
    const getAllRoleList = () => {
      const token = Cookies.get("token");
      getroleList(token)
        .then((res) => {
          const mappedRoles = res.result.map((role) => ({
            role_name: role.role_name || "", // Use empty string if null
            role_id: role.role_id || "", // Use empty string if null
          }));
          setRoleInfo(mappedRoles);
          showToast(res);
          console.log(res);
        })
        .catch((error) => {
          console.log("error during getting roleList:", error);
          showToast("An error occurred during fetching All role List");
        });
    };
    getAllRoleList();
  }, []);

  return (
    <div className="roles">
      <TopHeader />
      <div className="dashboardBody">
        <SidebarNav menuItems={SidebarMenuItems} />
        <div className="dashboardContent">
          <div className="headerBody">
            <h2>Roles</h2>
            <div className="actionHeader">
              <div className="formInput">
                <div className="inputField">
                  <input
                    type="text"
                    id="completionDate"
                    onChange={handleInputChange}
                    value={""}
                    placeholder="Search..."
                  />
                </div>
              </div>
              <Link to={"/roles/create"} className="tableBtn">
                + Create New Role
              </Link>
            </div>
          </div>
          <div className="rolesContainer">
            <div className="employeeDetails" style={{ overflowX: "auto" }}>
              <table className="employeeTable">
                <thead>
                  <tr>
                    <th>Role Name</th>
                    <th>Role ID</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {roleInfo.map((form, index) => {
                    return (
                      <tr>
                        <td>{roleInfo[index].role_name}</td>
                        <td>{roleInfo[index].role_id}</td>
                        <td className="actionLink">
                          {" "}
                          {/* <Link to={"/roles/edit"}>Edit</Link> */}
                          <Link
                            to="/roles/edit"
                            state={{ editRole: roleInfo[index] }}
                          >
                            Edit
                          </Link>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Roles;
