import React, { useState } from 'react';
import TopHeader from '../../components/TopHeader/TopHeader';
import SidebarNav from '../../components/SideBar/SideBar';
import SidebarMenuItems from '../../utils/SidebarMenuItems';
import './AdminDashboard.scss';

import { CircularProgressbar } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';

import { Chart as ChartJS, LineElement, PointElement, LinearScale, Title, CategoryScale } from 'chart.js';
import { Line } from 'react-chartjs-2';
import ChartDataLabels from 'chartjs-plugin-datalabels';





function AdminDashboard() {
    const [taskInfo, setTaskInfo] = useState({
        doerName: '',
        weekStartDate: '',
        weekEndDate: '',
        weekNumber: '',
    });
    const [activeItemId, setActiveItemId] = useState(1);


    const handleChange = (event) => {
        const { id, value } = event.target;
        setTaskInfo((prevTaskInfo) => ({
            ...prevTaskInfo,
            [id]: value,
        }));
    };
    const bodyNavigationItems = [
        { id: 1, name: 'Delegation' },
        { id: 2, name: 'Checklist' },
    ];
    const handleBodyNavigationClick = (itemId) => {
        setActiveItemId(itemId);
    };

    const incompletePerc = 66;
    const delayedPerc = 22;

    ChartJS.register(LineElement, PointElement, LinearScale, Title, CategoryScale, ChartDataLabels );

    const chartData = {
        labels: ["Jan", "Feb", "Mar", "Apr", "May", "Jun"],
        plugins: [ChartDataLabels],
        datasets: [
            {
                label: "First dataset",
                data: [33, 53, 85, 41, 44, 65],
                backgroundColor: "rgba(75,192,192,0.2)",
                borderColor: "rgba(75,192,192,1)"
            },
            {
                label: "Second dataset",
                data: [33, 25, 35, 51, 54, 76],
                borderColor: "#742774"
            }
        ]
    };

    return (
        <div className="adminDashboard">
            <TopHeader />
            <div className="dashboardBody">
                <SidebarNav menuItems={SidebarMenuItems} />
                <div className="dashboardContent">
                    <p className="title">Dashboard</p>
                    <div className="topInputFields">
                        <div className="formFields">
                            <div className='formInput'>
                                <label htmlFor="doerName">Doer Name</label>
                                <div className="inputField">
                                    <input
                                        type="text"
                                        id="doerName"
                                        onChange={handleChange}
                                        value={taskInfo.doerName}
                                        placeholder='Enter Doer Name'
                                    />
                                </div>
                            </div>
                            <div className='formInput'>
                                <label htmlFor="weekStartDate">Week Start Date</label>
                                <div className="inputField">
                                    <input
                                        type="date"
                                        id="weekStartDate"
                                        onChange={handleChange}
                                        value={taskInfo.weekStartDate}
                                    />
                                </div>
                            </div>
                            <div className='formInput'>
                                <label htmlFor="weekEndDate">Week End Date</label>
                                <div className="inputField">
                                    <input
                                        type="date"
                                        id="weekEndDate"
                                        onChange={handleChange}
                                        value={taskInfo.weekEndDate}
                                    />
                                </div>
                            </div>
                            <div className='formInput'>
                                <label htmlFor="weekNumber">Week Number</label>
                                <div className="inputField">
                                    <input
                                        type="number"
                                        id="weekNumber"
                                        onChange={handleChange}
                                        value={taskInfo.weekNumber}
                                        placeholder='Enter Week Number'
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="adminDashboardContainer">
                            <h2 className='bodyTitle'>Performance Score</h2>
                            <div className="bodyNavigation">
                                {bodyNavigationItems.map((item) => (
                                    <p
                                        key={item.id}
                                        className={`title ${activeItemId === item.id ? 'active' : ''}`}
                                        onClick={() => handleBodyNavigationClick(item.id)}
                                    >
                                        {item.name}
                                    </p>
                                ))}
                            </div>
                            <div className="statsBoxContainer">
                                <div className="statsBox">
                                    <div className="left">
                                        <p className="statTitle">Incomplete vs Completed</p>
                                        <p className="statNumber">66/100</p>
                                    </div>
                                    <div className="progressCircle">
                                        <CircularProgressbar value={incompletePerc} text={`${incompletePerc}%`} />;
                                    </div>
                                </div>
                                <div className="statsBox">
                                    <div className="left">
                                        <p className="statTitle">Delayed vs On time</p>
                                        <p className="statNumber">22/100</p>
                                    </div>
                                    <div className="progressCircle">
                                        <CircularProgressbar value={delayedPerc} text={`${delayedPerc}%`} />;
                                    </div>
                                </div>
                                <div className="statsBox">
                                    <div className="left">
                                        <p className="statTitle">Total Number of Tasks</p>
                                        <p className="statNumber">40</p>
                                    </div>
                                    {/* <div className="progressCircle">
                                            <CircularProgressbar value={percentage} text={`${percentage}%`} />;
                                    </div> */}
                                </div>
                            </div>
                        </div>
                        <div className="adminDashboardContainer">
                            <h2 className='bodyTitle'>Overall Performance</h2>
                            <div className="linearChat">

                                <Line data={chartData} plugins={[ChartDataLabels]} />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default AdminDashboard;
