import React, { useState, useEffect } from "react";
import TopHeader from "../../components/TopHeader/TopHeader";
import SidebarNav from "../../components/SideBar/SideBar";
import SidebarMenuItems from "../../utils/SidebarMenuItems";
import "./Employees.scss";
import { Link } from "react-router-dom";
import { showToasterMessage } from "../../utils/helper";
import Cookies from "js-cookie";
import getEmpList from "../../interface/implementation/getEmpListImp";

function Employees() {
  const handleInputChange = () => {
    console.log("create");
  };

  const showToast = (errorText) => {
    showToasterMessage(errorText);
  };

  const [empInfo, setEmpInfo] = useState([
    {
      user_id: "",
      emp_name: "",
      dept_name: "",
      emp_role: "",
      emp_type: "",
    },
  ]);

  useEffect(() => {
    const getAllEmpList = () => {
      const token = Cookies.get("token");
      getEmpList(token)
        .then((res) => {
          const mappedEmployees = res.result.map((employee) => ({
            user_id: employee.user_id || "",
            emp_name: employee.emp_name || "",
            dept_name: employee.dept_name || "",
            emp_role: employee.role_name || "",
            emp_type: "",
          }));
          setEmpInfo(mappedEmployees);
          showToast(res);
          console.log(res);
        })
        .catch((error) => {
          console.log("error during getting EmpList:", error);
          showToast("An error occurred during fetching All Employee List");
        });
    };
    getAllEmpList();
  }, []);

  return (
    <div className="employees">
      <TopHeader />
      <div className="dashboardBody">
        <SidebarNav menuItems={SidebarMenuItems} />
        <div className="dashboardContent">
          <div className="headerBody">
            <h2>Employees</h2>
            <div className="actionHeader">
              <div className="formInput">
                <div className="inputField">
                  <input
                    type="text"
                    id="completionDate"
                    onChange={handleInputChange}
                    value={""}
                    placeholder="Search..."
                  />
                </div>
              </div>
              <Link to={"/employees/create"} className="tableBtn">
                + Add New Employee
              </Link>
            </div>
          </div>
          <div className="employeesContainer">
            <div className="employeeDetails" style={{ overflowX: "auto" }}>
              <table className="employeeTable">
                <thead>
                  <tr>
                    <th>Employee Name</th>
                    <th>Department Name</th>
                    <th>Role</th>
                    <th>Employee Type</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {empInfo.map((form, index) => {
                    return (
                      <tr>
                        <td>{empInfo[index].emp_name}</td>
                        <td>{empInfo[index].dept_name}</td>
                        <td>{empInfo[index].emp_role}</td>
                        <td>{empInfo[index].emp_type}</td>
                        <td className="actionLink">
                          {" "}
                          {/* <Link to={"/employees/edit"}>Edit</Link> */}
                          <Link
                            to="/employees/edit"
                            state={{ editEmp: empInfo[index] }}
                          >
                            Edit
                          </Link>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Employees;
